import React, { Component } from "react";
import { InputNumber, Button, Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import Notice from "Components/Notice";
import { langCheck } from "Pub/js/utils";
import { getMulti } from "Pub/js/getMulti";
//
export default class MoneyModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            initVal: this.props.initVal,
            small: "",
            big: "",
            customScale: "",
            json: {}
        };
    }
    //
    componentWillReceiveProps(nextProps) {
        if (nextProps.modalVisibel !== true) {
            return;
        } else {
            this.setState({ initVal: nextProps.initVal }, () => {
                let { datatype } = nextProps;
                let { initVal } = this.state;
                //
                if (initVal === "" || initVal === null) {
                    this.setState({
                        customScale: datatype === "4" ? 0 : 2,
                        small: "",
                        big: ""
                    });
                } else {
                    let initArray = initVal && initVal.split(",");
                    if (datatype === "4") {
                        //
                        this.setState({
                            customScale: 0,
                            small: initArray && initVal.length === 3 ? initArray[1] : initVal.length === 2 ? initArray[0] : "", //3
                            big: initArray ? initArray[initArray.length - 1] : ""
                        });
                    } else {
                        //
                        this.setState({
                            customScale: initArray ? initArray[0] : 31,
                            small: initArray ? initArray[1] : "",
                            big: initArray ? initArray[2] : ""
                        });
                    }
                }
            });
        }
    }
    //
    showModalHidden = () => {
        this.props.setModalVisibel("moneyModalVisibel", false);
    };
    //dataval
    onOkDialog = () => {
        let { small, big, customScale, json } = this.state;
        let { datatype } = this.props;
        let result;
        if (small !== "" && big !== "") {
            if (Number(small) >= Number(big)) {
                return Notice({
                    status: "error",
                    msg: langCheck(
                        "ZoneSetting-000056",
                        "pages",
                        json
                    ) /*  */
                });
            }
        }
        if (datatype === "4") {
            //
            result = `${small},${big}`;
        } else {
            result = `${customScale},${small},${big}`;
        }
        this.props.handleSelectChange(result, "dataval");
        this.showModalHidden();
    };
    /**
     * 
     * @param {String} key
     * @param {String} val
     */
    saveValue = (key, val) => {
        if (_.isNull(val) || _.isUndefined(val)) {
            val = "";
        }
        this.setState({ [key]: val });
    };

    componentDidMount() {
        //
        let callback = json => {
            // console.log('json', json);
            this.setState({
                json: json
            });
        };
        getMulti({
            moduleId: "ZoneSetting",
            // currentLocale: 'zh-CN',
            domainName: "workbench",
            callback
        });
    }

    render() {
        let { customScale, small, big, json } = this.state;
        let { datatype } = this.props;
        return (
            <div className="myZoneModal">
                <HotKeysModal
                    closable={false}
                    customHeader={langCheck(
                        "ZoneSetting-000022",
                        "pages",
                        json
                    )} /*  */
                    // mask={false}
                    wrapClassName="zonesetting-moneyModal"
                    visible={this.props.modalVisibel}
                    onOk={this.onOkDialog}
                    destroyOnClose={true}
                    onCancel={this.showModalHidden}
                    footer={[
                        <Tooltip
                            placement="top"
                            overlayClassName="tootip-default"
                            title={`${langCheck(
                                "ZoneSetting-000005",
                                "pages",
                                json
                            )}(Alt + Y)`}
                        >
                            <Button
                                key="submit"
                                type="primary"
                                onClick={this.onOkDialog}
                            >
                                {langCheck("ZoneSetting-000005", "pages", json)}
                                (<span className="underline">Y</span>)
                                {/* /*  */}
                            </Button>
                        </Tooltip>,
                        <Tooltip
                            placement="top"
                            overlayClassName="tootip-default"
                            title={`${langCheck(
                                "ZoneSetting-000006",
                                "pages",
                                json
                            )}(Alt + N)`}
                        >
                            <Button key="back" onClick={this.showModalHidden}>
                                {langCheck("ZoneSetting-000006", "pages", json)}
                                (<span className="underline">N</span>)
                                {/* /*  */}
                            </Button>
                        </Tooltip>
                    ]}
                >
                    {(() => {
                        if (datatype !== "4") {
                            //
                            return (
                                <div>
                                    <div className="descrip_label">
                                        {langCheck(
                                            "ZoneSetting-000057",
                                            "pages",
                                            json
                                        )}{" "}
                                        {/* /*  */}
                                    </div>
                                    <div className="mdcontent">
                                        <div>
                                            <span className="money-label">
                                                {" "}
                                                {langCheck(
                                                    "ZoneSetting-000058",
                                                    "pages",
                                                    json
                                                )}
                                                {/* /*  :*/}
                                            </span>
                                            <InputNumber
                                                precision={0}
                                                min={0}
                                                max={8}
                                                value={customScale}
                                                onChange={value => {
                                                    this.saveValue(
                                                        "customScale",
                                                        value
                                                    );
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>
                            );
                        }
                    })()}
                    <div>
                        <div className="descrip_label">
                            {langCheck("ZoneSetting-000059", "pages", json)}{" "}
                        </div>
                        {/* /*  */}
                        <div className="mdcontent">
                            <div>
                                <span className="money-label">
                                    {langCheck(
                                        "ZoneSetting-000060",
                                        "pages",
                                        json
                                    )}
                                </span>
                                {/* /*  :*/}
                                <InputNumber
                                    precision={customScale}
                                    value={small}
                                    onChange={value => {
                                        this.saveValue("small", value);
                                    }}
                                />
                            </div>
                            <div>
                                <span className="money-label">
                                    {langCheck(
                                        "ZoneSetting-000061",
                                        "pages",
                                        json
                                    )}
                                </span>
                                {/* /*  :*/}
                                <InputNumber
                                    value={big}
                                    precision={customScale}
                                    onChange={value => {
                                        this.saveValue("big", value);
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                </HotKeysModal>
            </div>
        );
    }
}
