import React, { Component } from "react";
import _ from "lodash";
import Ajax from "Pub/js/ajax";
import { connect } from "react-redux";
import { Icon, Tree, Button, Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import Svg from "Components/Svg";
import { langCheck } from "Pub/js/utils";
import { getMulti } from "Pub/js/getMulti";
import {
    PageLayout,
    PageLayoutLeft,
    PageLayoutRight
} from "Components/PageLayout";
const TreeNode = Tree.TreeNode;
//
class RelateMetaModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            metaTree: [],
            selectTreeNodes: [],
            json: {}
        };
    }
    //
    onLoadData = treeNode => {
        let { json } = this.state;
        return new Promise(resolve => {
            if (treeNode.props.children) {
                resolve();
                return;
            }
            Ajax({
                url: `/nccloud/platform/templet/querymetapro.do`,
                info: {
                    name: langCheck(
                        "ZoneSetting-000017",
                        "pages",
                        json
                    ) /*  */,
                    action: langCheck(
                        "ZoneSetting-000020",
                        "pages",
                        json
                    ) /*  */
                },
                data: {
                    // metaid: metaid
                    metaid: treeNode.props.refpk
                },
                success: res => {
                    if (res) {
                        let { data, success } = res.data;
                        if (
                            success &&
                            data &&
                            data.rows &&
                            data.rows.length > 0
                        ) {
                            let metaTree = [];
                            data.rows.map((r, index) => {
                                metaTree.push({
                                    ...r,
                                    title: `${r.refcode} ${r.refname}`,
                                    key: `${treeNode.props.myUniqID}.${
                                        r.refcode
                                    }`,
                                    myUniqID: `${treeNode.props.myUniqID}.${
                                        r.refcode
                                    }`,
                                    isLeaf: r.isleaf
                                });
                            });
                            treeNode.props.dataRef.children = [].concat(
                                metaTree
                            );
                            this.setState({
                                metaTree: [...this.state.metaTree]
                            });
                            resolve();
                        }
                    }
                }
            });
        });
    };
    //keyvalue
    componentWillReceiveProps(nextProps) {
        let { json } = this.state;
        if (nextProps.modalVisibel !== true) {
            return;
        } else {
            const { metaid, relatemeta } = this.props.selectCard;
            let { cards } = this.props;
            if (relatemeta && relatemeta !== "") {
                let tmpArr1 = relatemeta.split(",");
                let tmpArr2 = [];
                //{codemyMetaPath}
                _.forEach(tmpArr1, t => {
                    let tmpArr = t.split("=");
                    tmpArr2.push({
                        code: tmpArr[0],
                        myMetaPath: tmpArr[1]
                    });
                });
                //
                _.forEach(cards, (c, index) => {
                    _.forEach(tmpArr2, t => {
                        if (t.code === c.code) {
                            c.myMetaPath = t.myMetaPath;
                        }
                    });
                });
            }
            Ajax({
                url: `/nccloud/platform/templet/querymetapro.do`,
                info: {
                    name: langCheck(
                        "ZoneSetting-000017",
                        "pages",
                        json
                    ) /*  */,
                    action: langCheck(
                        "ZoneSetting-000020",
                        "pages",
                        json
                    ) /*  */
                },
                data: {
                    metaid: metaid
                },
                success: res => {
                    if (res) {
                        let { data, success } = res.data;
                        if (
                            success &&
                            data &&
                            data.rows &&
                            data.rows.length > 0
                        ) {
                            let metaTree = [];
                            data.rows.map((r, index) => {
                                metaTree.push({
                                    ...r,
                                    title: `${r.refcode} ${r.refname}`,
                                    key: `${r.refcode}`,
                                    myUniqID: `${r.refcode}`,
                                    isLeaf: r.isleaf
                                });
                            });
                            this.setState({ metaTree: metaTree });
                        } else {
                            if (
                                success &&
                                data &&
                                data.rows &&
                                !data.rows.length
                            ) {
                                Notice({
                                    status: "warning",
                                    msg: langCheck(
                                        "ZoneSetting-000021",
                                        "pages",
                                        json
                                    ) /*  */
                                });
                            }
                        }
                    }
                }
            });
        }
    }
    //
    showModalHidden = () => {
        let { cards } = this.props;
        _.forEach(cards, (c, i) => {
            if (c.myMetaPath && c.myMetaPath !== "") {
                c.myMetaPath = "";
            }
        });
        this.props.setModalVisibel("relateMetaModalVisibel", false);
    };
    //relatemeta{}
    onOkDialog = () => {
        let result = "";
        let { cards } = this.props;
        _.forEach(cards, (c, i) => {
            if (c.myMetaPath && c.myMetaPath !== "") {
                if (result === "") {
                    result = `${c.code}=${c.myMetaPath}`;
                } else {
                    result = `${result},${c.code}=${c.myMetaPath}`;
                }
            }
        });
        this.props.handleSelectChange(result, "relatemeta");
        this.showModalHidden();
    };
    //
    renderTreeNodes = data => {
        return data.map(item => {
            if (item.children) {
                return (
                    <TreeNode
                        icon={({ expanded }) => {
                            return (
                                <Svg
                                    width={20}
                                    height={20}
                                    xlinkHref={
                                        expanded
                                            ? "#icon-wenjianjiadakai"
                                            : "#icon-wenjianjia"
                                    }
                                />
                            );
                        }}
                        switcherIcon={({ expanded }) => {
                            return (
                                <i
                                    className={`font-size-18 iconfont ${
                                        expanded
                                            ? "icon-shu_zk"
                                            : "icon-shushouqi"
                                    }`}
                                />
                            );
                        }}
                        title={item.title}
                        key={item.key}
                        dataRef={item}
                    >
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            if (item.isLeaf) {
                return (
                    <TreeNode
                        icon={<span className="tree-dot" />}
                        {...item}
                        dataRef={item}
                    />
                );
            } else {
                return (
                    <TreeNode
                        {...item}
                        icon={({ expanded }) => {
                            return (
                                <Svg
                                    width={20}
                                    height={20}
                                    xlinkHref={
                                        expanded
                                            ? "#icon-wenjianjiadakai"
                                            : "#icon-wenjianjia"
                                    }
                                />
                            );
                        }}
                        switcherIcon={({ expanded }) => {
                            return (
                                <i
                                    className={`font-size-18 iconfont ${
                                        expanded
                                            ? "icon-shu_zk"
                                            : "icon-shushouqi"
                                    }`}
                                />
                            );
                        }}
                        dataRef={item}
                    />
                );
            }
        });
    };
    //
    onSelect = (selectedKeys, info) => {
        this.setState({ selectedKeys, selectTreeNodes: info.selectedNodes });
    };
    //
    cardClick = card => {
        let { selectTreeNodes } = this.state;

        if (selectTreeNodes.length > 0) {
            const tmpDataRef = selectTreeNodes[0].props.dataRef;
            if (tmpDataRef.datatype === card.datatype) {
                card.myMetaPath = tmpDataRef.myUniqID;
            } else {
                return;
            }
        } else {
            return;
        }

        this.setState({ selectedKeys: [], selectTreeNodes: [] });
    };
    //
    deleteMyMetaPath = (card, index) => {
        card.myMetaPath = "";
        this.setState({ selectedKeys: [], selectTreeNodes: [] });
    };
    /**
     * 
     * @param {String} cardDataType 
     * @param {String} selectTreeDataType 
     * @return {Number} result 
     */
    getOpacity = (cardDataType, selectTreeDataType) => {
        let result = 1;
        if (selectTreeDataType !== "") {
            if (cardDataType !== selectTreeDataType) {
                result = 0.5;
            }
        }
        return result;
    };
    componentDidMount() {
        //
        let callback = json => {
            // console.log('json', json);
            this.setState({
                json: json
            });
        };
        getMulti({
            moduleId: "ZoneSetting",
            // currentLocale: 'zh-CN',
            domainName: "workbench",
            callback
        });
    }
    render() {
        const { metaTree, selectTreeNodes, json } = this.state;
        const { cards } = this.props;
        let selectTreeNodeDatatype = "";
        if (selectTreeNodes.length > 0) {
            selectTreeNodeDatatype = selectTreeNodes[0].props.dataRef.datatype;
        }
        return (
            <HotKeysModal
                // closable={false}
                title={
                    <div>
                        {langCheck("ZoneSetting-000071", "pages", json)}
                        {/* /*  */}
                        <span style={{ fontSize: "13px", marginLeft: "10px" }}>
                            {langCheck("ZoneSetting-000072", "pages", json)}
                            {/* /*  */}
                        </span>
                    </div>
                }
                // mask={false}
                wrapClassName="realate-meta-modal"
                visible={this.props.modalVisibel}
                onOk={this.onOkDialog}
                destroyOnClose={true}
                onCancel={this.showModalHidden}
                width={"80%"}
                bodyHeight={`${document.body.clientHeight * 0.72}px`}
                // style={{ top: 20 }}
                footer={[
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000005",
                            "pages",
                            json
                        )}(Alt + Y)`}
                    >
                        <Button
                            key="submit"
                            // disabled={}
                            type="primary"
                            onClick={this.onOkDialog}
                        >
                            {langCheck("ZoneSetting-000005", "pages", json)}(
                            <span className="underline">Y</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>,
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000006",
                            "pages",
                            json
                        )}(Alt + N)`}
                    >
                        <Button key="back" onClick={this.showModalHidden}>
                            {langCheck("ZoneSetting-000006", "pages", json)}(
                            <span className="underline">N</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>
                ]}
            >
                <PageLayout>
                    <PageLayoutLeft>
                        <div className="sider-tree">
                            <Tree
                                loadData={this.onLoadData}
                                showLine
                                showIcon
                                onSelect={this.onSelect}
                                selectedKeys={this.state.selectedKeys}
                            >
                                {this.renderTreeNodes(metaTree)}
                            </Tree>
                        </div>
                    </PageLayoutLeft>
                    <PageLayoutRight>
                        <div className="sider-list">
                            <table>
                                <thead>
                                    <tr>
                                        <th>
                                            {langCheck(
                                                "ZoneSetting-000073",
                                                "pages",
                                                json
                                            )}
                                        </th>
                                        {/* /*  */}
                                        <th>
                                            {langCheck(
                                                "ZoneSetting-000074",
                                                "pages",
                                                json
                                            )}
                                        </th>
                                        {/* /*  */}
                                    </tr>
                                </thead>
                                <tbody>
                                    {cards.map((c, i) => {
                                        //.,
                                        //
                                        if (c.metapath && c.metapath !== "") {
                                            return (
                                                <tr
                                                    key={i}
                                                    onClick={e => {
                                                        this.cardClick(c);
                                                    }}
                                                    style={{
                                                        opacity: this.getOpacity(
                                                            c.datatype,
                                                            selectTreeNodeDatatype
                                                        )
                                                    }}
                                                >
                                                    <td>{c.label}</td>
                                                    {(() => {
                                                        if (
                                                            c.myMetaPath &&
                                                            c.myMetaPath !== ""
                                                        ) {
                                                            return (
                                                                <td>
                                                                    <div className="close-and-text">
                                                                        <Icon
                                                                            type="close"
                                                                            onClick={() => {
                                                                                this.deleteMyMetaPath(
                                                                                    c,
                                                                                    i
                                                                                );
                                                                            }}
                                                                        />
                                                                        &nbsp;
                                                                        {
                                                                            c.myMetaPath
                                                                        }
                                                                    </div>
                                                                </td>
                                                            );
                                                        } else {
                                                            return <td />;
                                                        }
                                                    })()}
                                                </tr>
                                            );
                                        }
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </PageLayoutRight>
                </PageLayout>
            </HotKeysModal>
        );
    }
}
export default connect(
    state => ({
        selectCard: state.zoneSettingData.selectCard
    }),
    {}
)(RelateMetaModal);
