import React from "react";
import { high } from "nc-lightapp-front";
import {langCheck} from "Pub/js/utils";
const { Refer } = high;

export default function(props = {}) {
    var conf = {
        refType: "tree",
        refName: langCheck('0000PUB-000060'),/*  */
        placeholder: langCheck('0000PUB-000060'),/*  */
        refCode: "uapbd.refer.userdef.DefdocTreeRef",
        queryTreeUrl: "/nccloud/uapbd/userdef/DefdocTreeRef.do",
        isMultiSelectedEnabled: false,
        columnConfig: [{ name: [langCheck('0000PUB-000032'), langCheck('0000PUB-000033')], code: ["code", "name"] }]/*  ,*/
    };
    return <Refer {...Object.assign(conf, props)} />;
}
