import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Checkbox, Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import DefdocGridRef from "Components/Refers/DefdocGridRef";
import DefdocTreeRef from "Components/Refers/DefdocTreeRef";
import { langCheck } from "Pub/js/utils";
import { getMulti } from "Pub/js/getMulti";

class CusDocDefaultValueModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            defaultvalueObj: {}
        };
    }
    //
    componentWillReceiveProps(nextProps) {
        if (nextProps.modalVisibel !== true) {
            return;
        }
        let defaultvalueObj = {};
        const { defaultvalue, refname } = nextProps;
        //
        if (defaultvalue) {
            const tmpDefaultvalueList = defaultvalue.split("=");
            defaultvalueObj = {
                display: tmpDefaultvalueList[0],
                value: tmpDefaultvalueList[1]
            };
        }
        this.setState({ defaultvalueObj });
    }

    //
    showModalHidden = () => {
        this.props.setModalVisibel("customDocDefaultModalVisibel", false);
    };
    //dataval
    onOkDialog = () => {
        let { defaultvalueObj } = this.state;
        const display = defaultvalueObj.display;
        const value = defaultvalueObj.value;
        let defaultvalue = "";
        //refpk
        if (display !== "" && value !== "") {
            defaultvalue = `${display}=${value}`;
        }
        this.props.handleSelectChange(defaultvalue, "defaultvalue");
        this.showModalHidden();
    };

    //isgrand
    renderDocRefer = () => {
        const { isgrade, docPk, refname, json } = this.props;
        const { defaultvalueObj } = this.state;
        const referProps = {
            foolValue: defaultvalueObj,
            queryCondition: () => ({
                pk_defdoclist: docPk
            }),
            refName: refname,
            placeholder: refname,
            onChange: (ref, foolValue) => {
                this.setState({
                    defaultvalueObj: foolValue
                });
            }
        }
        
        //
        if(isgrade === 'Y') {
            return <DefdocTreeRef {...referProps}/>;
        } else { //
            return <DefdocGridRef {...referProps}/>;
        }
    }
    render(){
        const { json } = this.props;
        return <HotKeysModal
                closable={false}
                title={langCheck("ZoneSetting-000227", "pages", json)} /*  */
                // mask={false}
                wrapClassName="zonesetting-defaultValueModal"
                visible={this.props.modalVisibel}
                onOk={this.onOkDialog}
                destroyOnClose={true}
                onCancel={this.showModalHidden}
                footer={[
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000005",
                            "pages",
                            json
                        )}(Alt + Y)`}
                    >
                        <Button
                            key="submit"
                            type="primary"
                            onClick={this.onOkDialog}
                        >
                            {langCheck("ZoneSetting-000005", "pages", json)}
                            (<span className="underline">Y</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>,
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000006",
                            "pages",
                            json
                        )}(Alt + N)`}
                    >
                        <Button key="back" onClick={this.showModalHidden}>
                            {langCheck("ZoneSetting-000006", "pages", json)}
                            (<span className="underline">N</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>
                ]}
            >
                <div className="mdcontent">
                    {this.renderDocRefer()}
                </div>
            </HotKeysModal>
    }
}

export default CusDocDefaultValueModal;