import React, { Component } from 'react';

class DragArea extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    
    //children
    renderChildren = () => {
        const { children, onDragItemClick } = this.props;
        return React.Children.map(children, (child) => {
            //
            return React.cloneElement(child, {
                onDragItemClick: onDragItemClick && onDragItemClick.bind(this, child.props)
            });
        });
    }

    render() {
        const { headerTitle, renderHeader } = this.props;
        return (
            <div className="side-panel">
                <div className="panel-header">
                {
                    renderHeader ? renderHeader : 
                    <span className="title">{headerTitle}</span>
                }
                </div>
                <div className="panel-content clearfix">
                    {this.renderChildren()}
                </div>
            </div>
        )
    }
}

export default DragArea;