import { DragSource, DropTarget } from 'react-dnd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { updateSelectArea, updateSelectCard } from "Store/ZoneSetting/action";
import { getItemTypeCom } from './createCard';
import { removeSelectCardCls } from './events';
import { langCheck } from 'Pub/js/utils';
import Notice from "Components/Notice";
import _ from "lodash";
//
const cardSource = {
	beginDrag(props) {
		return {
			id: props.id,
			index: props.index,
			areaIndex: props.areaIndex
		};
	},
	isDragging(props, monitor) {
		// If your component gets unmounted while dragged
		// (like a card in Kanban board dragged between lists)
		// you can implement something like this to keep its
		// appearance dragged:
		return monitor.getItem().id === props.id;
	}
};

const cardTarget = {
	hover(props, monitor, component) {
		const dragIndex = monitor.getItem().index;
		const hoverIndex = props.index;

		// Don't replace items with themselves
		if (dragIndex === hoverIndex) {
			return;
		}
	},
	drop(props, monitor, component) {
		const item = monitor.getItem();
		const dropIndex = props.index;
		if (monitor.didDrop() || item.index === dropIndex || item.areaIndex !== props.areaIndex) {
			// If you want, you can check whether some nested
			// target already handled drop
			return;
		}
		
		props.moveCard(item.index, dropIndex, props.areaIndex);
	}
};

@DropTarget('card', cardTarget, (connect, monitor) => ({
	connectDropTarget: connect.dropTarget(),
	isOver: monitor.isOver()
}))
@DragSource('card', cardSource, (connect, monitor) => ({
	connectDragSource: connect.dragSource(),
	isDragging: monitor.isDragging()
}))
class MyCard extends Component {
	constructor(props) {
		super(props);
		this.state = {
			radioSelectedValue: "", //
			passwordValue: "" //
		};
	}

	shouldComponentUpdate(nextProps,nextState) {
		//
		if(!_.isEqual(nextProps.selectCard, this.props.selectCard)){
			return false;
		}
		//
		if(nextProps.shouldHideGray !== this.props.shouldHideGray){
			return true;
		}
		//
		if (nextProps.areaItem.areatype !== this.props.areaItem.areatype) {
			return true;
		}
		//
		// isDraggingfalse
		// if (_.isEqual(nextProps.areaItem.queryPropertyList, this.props.areaItem.queryPropertyList)
		// 	&& nextProps.areaItem.pk_area !== this.props.selectCard.areaid){
		// // if (_.isEqual(nextProps.areaItem.queryPropertyList, this.props.areaItem.queryPropertyList)){
        //     return false;
        // }
		return true;
	}

	//
	deleteCard = (e) => {
		e.stopPropagation();
		let thisCard = this.props.areaList[this.props.areaIndex].queryPropertyList[this.props.index];
		//
		if (this.props.isImplement && thisCard.isfixedcondition == true) {
			Notice({ status: "warning", msg: langCheck('ZoneSetting-000231', 'pages', this.props.json) }); /*  */
		} else {
			this.props.deleteCard(this.props.index);
		}
	};
	//
	selectThisCard = (e) => {
		e.stopPropagation();
		this.updateThisCard(e);
		if (!_.isEmpty(this.props.selectedArea)) this.props.updateSelectArea({}); //
	};

	//
	updateThisCard = (e) => {
		let { areaList, areaIndex, index } = this.props;
		let card = areaList[areaIndex].queryPropertyList[index];
		this.updateActiveCardCls(e);
		this.props.updateSelectCard(card);
	};

	//className
	updateActiveCardCls = e => {
		removeSelectCardCls();
		e && e.currentTarget && e.currentTarget.classList.add("select-card");
	}

	//className
	getCardClassName = () => {
		const { selectCard, required, id, areaid } = this.props;
		let result = '';
		if (required) {
			result = ' required-card';
		}
		if (selectCard.pk_query_property === id && selectCard.areaid === areaid) {
			result = `select-card${result}`;
		} 
		else {
			result = `normal-card${result}`;
		}
		return result;
	};

	//
	renderCard = ({ opacity, showName }) => {
		const { areaType, color, cardInfo, visible, selectCard, isOver, json } = this.props;
		const myClassName = this.getCardClassName();
		const prefix = areaType !== 'table' ? 'card' : areaType;
		const cardProps = { ...cardInfo, onClick: this.selectThisCard };
		const hoverCls = isOver ? ' hover' : '';
		let tableColWidth = {}; 
		//
		if (cardInfo.pk_form_property === selectCard.pk_form_property && prefix === 'table' && !isNaN(selectCard.width)){
			tableColWidth.minWidth = `${selectCard.width}px`;
			if (selectCard.width && +selectCard.width < 120){
				tableColWidth.minWidth = 120;
			}
		}
		return (
			<li className={`property-item ${prefix}-content ${myClassName}${hoverCls}`} style={{ opacity: opacity }} onClick={this.selectThisCard}>
				<div style={{ color: color }} className={`${prefix}-item`} title={showName} style={tableColWidth}>
					{
						areaType !== 'search' && 
						<div className="item-name">
							<span className="title">{showName}</span>
						</div>
					}
					<div className="item-content">{getItemTypeCom.call(this, cardProps, areaType, json)}</div>
					<span className='delete-card' onClick={this.deleteCard}> x </span>
					{!visible && <span className="hidden-tag"></span>}
				</div>
			</li>
		)
	}
	
	render() {
		const {
			name,
			visible,
			shouldHideGray,
			isDragging,
			connectDragSource,
			connectDropTarget
		} = this.props;
		console.log('render card')
		// const opacity = visible ? 1 : 0.5;
		const opacity = isDragging ? 0.3 : visible ? 1 : 0.6;
		const showName = _.isEmpty(name) ? String.fromCharCode(160) : name;
		if(shouldHideGray===true && visible===false){
			return null;
		}
		return connectDragSource(
			connectDropTarget(this.renderCard({ opacity, showName }))
		);
	}
}
export default connect(
	(state) => ({
		selectCard: state.zoneSettingData.selectCard,
		selectedArea: state.zoneSettingData.selectedArea,
		areaList: state.zoneSettingData.areaList,
	}),
	{ 
		updateSelectArea,
		updateSelectCard
	}
)(MyCard);
