import React, { Component } from "react";
import { Icon, Checkbox, Popconfirm, Button, Tooltip } from "antd";
import { connect } from "react-redux";
import MyCard from "./card";
import { updateSelectCard, updateSelectArea, updateBatchSource, updateAreaList } from "Store/ZoneSetting/action";
import BatchSettingModal from "./batchSettingModal";
import AddNotMetaDataModal from "./addNotMetaDataModal";
import { DragSource, DropTarget } from 'react-dnd';
import { langCheck } from 'Pub/js/utils';
import { addNewAreaItem, removeSelectCardCls, findRepeatItemsByCode } from './events';
import Notice from "Components/Notice";
import FormGroupArea from './components/FormGroupArea';
import TableFoldArea  from './components/TableFoldArea';
import TableDrawerArea  from './components/TableDrawerArea';
import GrandsonArea from './components/GrandsonArea';
//
const areaSource = {
    beginDrag(props) {
        if (!props.isImplement){
            props.updateSelectArea(props.areaItem); //
            removeSelectCardCls(); //
        }
        return {
            id: props.id,
            index: props.index,
            type: props.type
        };
    },
    canDrag(props) {
        //
        return !props.isImplement;
    }
};

const areaTarget = {
    drop(props, monitor, component){
        const dragItem = monitor.getItem();
        //
        if (dragItem.type === 'card') {
            //
            if (props.isImplement && props.areatype === '0') return;
            component.openAddNotMetaInArea();
            //
            if(monitor.isOver({ shallow: false })){
                component.setState({
                    currentNotMeta: { datatype: dragItem.datatype },
                    realAreaIndex: props.index
                });
            }
        } else if (dragItem.from !== 'left') {
            const dragIndex = dragItem.index;
            const hoverIndex = props.index;

            // Don't replace items with themselves
            if (dragIndex === hoverIndex) return;

            props.onDragArea(dragIndex, hoverIndex);
            monitor.getItem().index = hoverIndex;
        }
    }
};


//DropSource
@DragSource('area', areaSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging()
}))
//DropTarget
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver()
}))
class AreaItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isShow: props.isShow,
            batchSettingModalVisibel: false,
            addDataModalVisibel: false,
            shouldHideGray: false,
            currentNotMeta: {}, //
            realAreaIndex: props.index, //areaListindex
            groupShowArr: props.areaItem.groups ? [...props.areaItem.groups].fill(true) : [], //
            groupNotVisibleArr: props.areaItem.groups ? [...props.areaItem.groups].fill(false) : [], //
            foldObj: props.areaItem.sides ? { //
                foldShow: false,
                foldNotVisible: false,
                emptyArea: {} //
            } : {},
            sideObj: props.areaItem.sides ? { //
                sideShow: false,
                sideNotVisible: false,
                emptyArea: {} //
            } : {},
            grandsonObj: { //
                grandsonShowArr: props.areaItem.grandsons ? [...props.areaItem.grandsons].fill(true) : [], //
                grandsonNotVisibleArr: props.areaItem.grandsons ? [...props.areaItem.grandsons].fill(false) : [] //
            },
            oprWidth: 150 //
        };
    }

     componentDidMount(){
        //
        if(this.props.areaListLength == 1){
            this.showOrHideAreaItem(true);
        }
     }
    
    //
    // moveCard = (dragIndex, hoverIndex) => {
    //     this.props.moveCard(dragIndex, hoverIndex, this.props.index);
    // };
    /**
     * 
     * @param {Number} dragIndex index
     * @param {Number} hoverIndex index
     * @param {Number} areaIndex index
     **/
    moveCard = (dragIndex, hoverIndex, areaIndex) => {
        let { areaList } = this.props;
        areaList = _.cloneDeep(areaList);
        const cards = areaList[areaIndex].queryPropertyList;

        const dragCard = cards[dragIndex];
        cards.splice(dragIndex, 1);
        cards.splice(hoverIndex, 0, dragCard);
        _.forEach(cards, (q, i) => {
            q.position = i + 1;
        });
        this.props.updateAreaList(areaList);
        // this.props.updateSelectCard({}); //
    };

    /**
     * 
     *
     * @param cardIndex - index
     * @param areaIndex - index
     */
    deleteCard = (cardIndex, areaIndex) => {
        this.props.deleteCard(cardIndex, areaIndex);
    };
    // areaId:id
    addMetaInArea = ({
        metaId = this.props.areaItem.metaid,
        areaId = this.props.id,
        areaType = this.props.areatype
    } = {}) => {
        this.props.addMetaInArea(
            metaId,
            // this.props.metaid,
            areaId,
            areaType
        );
    };
    //
    // selectThisCard = (e, cardIndex) => {
    //     this.props.selectThisCard(e, cardIndex, this.props.index);
    // };
    //
    openBatchSetting = (areaIndex) => {
        this.props.updateSelectCard({});
        this.setModalVisibel(true, areaIndex);
    };
    ///
    setModalVisibel = (visibel, areaIndex) => {
        const { areaList, index } = this.props;
        let newIndex = areaIndex || index;
        this.props.updateBatchSource(areaList[newIndex].queryPropertyList);
        this.setState({ 
            batchSettingModalVisibel: visibel,
            realAreaIndex: newIndex
        });
    };
    //
    openAddNotMetaInArea = () => {
        this.setAddDataModalVisibel(true);
    };
    ///
    setAddDataModalVisibel = visibel => {
        this.setState({ addDataModalVisibel: visibel });
    };

    //idindex
    getAreaIndexByPk = (area) => {
        if(!area) return;
        let areaIndex;
        this.props.areaList.map((item, index) => {
            if (item.pk_area === area.pk_area) {
                areaIndex = index;
            }
        });
        return areaIndex;
    }

    ///
    showOrHideAreaItem = (flag) => {
        let isShow = typeof flag !== 'boolean' ? !this.props.isShow : flag;
        this.props.onExpandArea(this.props.index, isShow);
        if (isShow){
            //
            setTimeout(() => {
                this.setState({ oprWidth: this.oprColRef && this.oprColRef.clientWidth });
            }, 0);
        }
    };

    ///
    toggleFormGroup = (index) => {
        let { groupShowArr } = this.state;
        groupShowArr[index] = !groupShowArr[index];
        this.setState({ groupShowArr });
    }

    //
    clearSelectCard = () => {
        removeSelectCardCls();
        this.props.updateSelectCard({});
    }

    //
    selectAreaItem = (e, area = this.props.areaItem) => {
        e.stopPropagation();
        const { selectCard, selectedArea } = this.props;
        if (!_.isEmpty(selectCard)) this.clearSelectCard();//
        if (selectedArea && area.pk_area !== selectedArea.pk_area) this.props.updateSelectArea(area);
    }

    //
    checkAreaCodeRepeat = (areaList) => {
        const repeatArea = findRepeatItemsByCode(areaList); //
        if (repeatArea && repeatArea.length > 0) {
            let repeatNames = repeatArea.map(item => item.name);
            Notice({ status: 'error', msg: `${langCheck('ZoneSetting-000046', "pages", this.props.json)}${JSON.stringify(repeatNames)}${langCheck('102202APP-000011', "pages", this.props.json)}` }); /*  */
            return true;
        }
        return false;
    }

    // position:
    handleAddFormGroup = (e, position) => {
        e.stopPropagation();
        const { areaItem, areaList, zoneFormData, json } = this.props;
        const relationcode = zoneFormData.headcode;
        const groupPosition = _.findLastIndex(areaList, item => item.relationcode === relationcode) + 1;
        //
        if (!this.props.isShow) {
            this.showOrHideAreaItem(true);
        }
        //
        setTimeout(() => {
            let addArea = this.addArea(this.props, {
                items: {
                    areatype: '1',
                    relationcode
                },
                position: position || groupPosition
            });
            if (addArea && position === undefined){
                //
                let lastFormGroupElm = this[`formGroupRef${areaItem.groups.length - 1}`];
                lastFormGroupElm.scrollIntoView();
            }
        }, 0);
    }

    //
    handleHideGray = e => {
        this.setState({
            shouldHideGray: e.target.checked
        });
    }

    //
    handleGroupHideGray = (e, groupIndex) => {
        let { groupNotVisibleArr } = this.state;
        groupNotVisibleArr[groupIndex] = e.target.checked;
        this.setState({ groupNotVisibleArr });
    }
    

    //
    getGroupContentVisible = (groupIndex) => {
        return this.state.groupShowArr[groupIndex] || groupIndex >= this.state.groupShowArr.length; //index
    }

    //
    getGrandsonContentVisible = (grandsonIndex) => {
        const { grandsonShowArr } = this.state.grandsonObj;
        return grandsonShowArr[grandsonIndex] || grandsonIndex >= grandsonShowArr.length; //index
    }

    /**
     * 
     *
     * @monitor dropTargetmonitor
     * @areaIndex index
     */
    handleSpecialAreaDrop = (monitor, areaIndex) => {
        const dragItem = monitor.getItem();
        this.openAddNotMetaInArea();
        this.setState({
            realAreaIndex: areaIndex,
            currentNotMeta: { datatype: dragItem.datatype }
        });
    }
    
    /**
     * 
     *
     * @param type - /
     * @param index - index
     * @param child - //
     */
    checkAreaIsBeginOrEnd = ({ type, index, child }) => {
        const currArea = this.props.areaList[index]; //
        const notNullArea = this.props.newAreaList.filter(item => item !== null);
        const firstArea = notNullArea[0];
        const lastArea = notNullArea[notNullArea.length - 1];
        const childType = child && Object.keys(child)[0];
        const childArea = child && Object.values(child)[0];
        const children = childType && this.props.areaItem[childType];
        //
        if(type === 'begin'){
            if (childArea) {
                return children[0].pk_area === childArea.pk_area;
            } else {
                return firstArea.pk_area === currArea.pk_area;
            }
        }else if(type === 'end'){
            //
            if (childArea) {
                return children[children.length - 1].pk_area === childArea.pk_area;
            } else {
                return lastArea.pk_area === currArea.pk_area;
            }
        }
    }

    //
    handleDeleteArea = (areaId) => {
        return new Promise(resolve => {
            let deleteCallback;
            const { areaItem, onDeleteArea } = this.props;
            if (areaItem.groups && areaItem.groups.length > 0){
                /*  */
                deleteCallback = () => Notice({ status: "warning", msg: langCheck('ZoneSetting-000210', 'pages', this.props.json) });
            }
            onDeleteArea(areaId, deleteCallback);
            this.props.updateSelectArea({});
            resolve();
        })
    }

    // 
    addArea = (...args) => {
        //
        if (this.checkAreaCodeRepeat(this.props.areaList)) {
            return false;
        }
        return addNewAreaItem.apply(this, args);
    }

    //
    handleToggleFoldForm = (e) => {
        let { foldObj } = this.state;
        let foldArea = this.props.areaItem.sides && this.props.areaItem.sides.find(item => item.areastatus === 'browse');
        //
        if (this.checkAreaCodeRepeat(this.props.areaList)) {
            return;
        }
        if (!this.props.isImplement && !foldArea){
            //
            foldArea = this.addArea(this.props, {
                items: {
                    areatype: '1',
                    areastatus: 'browse',
                    relationcode: this.props.areaItem.code
                }
            });
            foldObj.emptyArea = _.cloneDeep(foldArea); //  
        }
        foldObj.foldShow = !foldObj.foldShow;
        this.setState({ foldObj }, () => {
            if (!this.state.foldObj.foldShow){
                //
                if (!this.props.isImplement && _.isEqual(foldArea, this.state.foldObj.emptyArea)){
                    this.handleDeleteArea(foldArea.pk_area);
                }
            }
        });
        this.selectAreaItem(e, foldArea);
    }
    
    //
    handleToggleSideForm = (e) => {
        let { sideObj } = this.state;
        const { areaItem } = this.props;
        let sideArea = areaItem.sides && areaItem.sides.find(item => item.areastatus === 'edit');
        const hasGrandsonArea = areaItem.grandsons && areaItem.grandsons.length > 0; //
        //
        if (!this.props.isImplement && !sideArea && !hasGrandsonArea) {
            sideArea = this.addArea(this.props, {
                items: {
                    areatype: '1',
                    areastatus: 'edit',
                    relationcode: areaItem.code
                }
            });
            sideObj.emptyArea = _.cloneDeep(sideArea); //  
        }
        if (sideArea || hasGrandsonArea) {
            if (!sideArea) sideArea = areaItem; //
            sideObj.sideShow = !sideObj.sideShow;
            this.setState({ sideObj }, () => {
                if (sideObj.sideShow === true){
                    this.selectAreaItem(e, sideArea);
                }else{
                    //
                    if (!this.props.isImplement && _.isEqual(sideArea, this.state.sideObj.emptyArea)) {
                        this.handleDeleteArea(sideArea.pk_area);
                    }
                    this.props.updateSelectArea({});
                }
            });
        }
    }

    ///
    handleToggleGrandson = (index) => {
        let { grandsonObj } = this.state; 
        grandsonObj.grandsonShowArr[index] = !grandsonObj.grandsonShowArr[index];
        this.setState({ grandsonObj });
    }

    //
    handleFoldHideGray = e => {
        let { foldObj } = this.state;
        foldObj.foldNotVisible = e.target.checked;
        this.setState({ foldObj });
    }

    //
    handleGrandsonHideGray = (e, grandsonIndex) => {
        let { grandsonObj } = this.state;
        grandsonObj.grandsonNotVisibleArr[grandsonIndex] = e.target.checked;
        this.setState({ grandsonObj });
    }

    //
    copyArea = (copyItem, position) => {
        const { templetid, code, metaid, metaname, queryPropertyList } = this.props.areaItem;
        const queryPropertyListCopy = this.copyAreaProperty(queryPropertyList);
        const commonAreaItemsCopy = {
            relationcode: code,
            templetid,
            metaid,
            metaname,
            queryPropertyList: queryPropertyListCopy
        }
        let newItem = Object.assign(commonAreaItemsCopy, copyItem);
        return this.addArea(this.props, {
            items: newItem,
            position
        });
    }

    //
    copyAreaProperty = (queryPropertyList) => {
        let result = [];
        //
        const newCopyItemArr = [
            'areaid',
            'code',
            'datatype',
            'label',
            'metapath',
            'color',
            'isrevise',
            'required',
            'disabled',
            'visible',
            'maxlength',
            'defaultvalue',
            'hyperlinkflag',
            'defaultvar',
            'define1',
            'define2',
            'define3',
            'itemtype',
            'myrefpk'
        ];
        
        queryPropertyList.forEach(copyItem => {
            let newCopyObj = {};
            newCopyItemArr.forEach(newItem => {
                let newValue = copyItem[newItem] !== undefined ? copyItem[newItem] : '';
                newCopyObj[newItem] = newValue;
            });
            newCopyObj.pk_query_property = ''; //
            newCopyObj.areaid = ''; //id
            result.push(newCopyObj);
        });
        return result;
    }

    //
    handleCopyFoldArea = (copyPosition) => {
        const { areaItem } = this.props;
        let { foldObj } = this.state;
        //
        if (this.checkAreaCodeRepeat(this.props.areaList)) {
            return;
        }
        if (areaItem.sides && areaItem.sides.find(item => item.areastatus === 'browse')){
            Notice({ status: "warning", msg: langCheck('ZoneSetting-000213', 'pages', this.props.json) });/*  */
            return;
        }
        const copyArea = this.copyArea({
            areatype: '1',
            areastatus: 'browse',
            code: areaItem.code + '_01',
            name: areaItem.name + '_01',
        }, copyPosition);
        if (copyArea){
            foldObj.foldShow = true; //
            this.setState({ foldObj });
        }
    }

    //
    handleCopySideArea = (copyPosition) => {
        const { areaItem } = this.props;
        let { sideObj } = this.state;
        if (areaItem.sides && areaItem.sides.find(item => item.areastatus === 'edit')){
            Notice({ status: "warning", msg: langCheck('ZoneSetting-000214', 'pages', this.props.json) });/*  */
            return;
        }
        const copyArea = this.copyArea({
            areatype: '1',
            areastatus: 'edit',
            code: areaItem.code + '_02',
            name: areaItem.name + '_02',
        }, copyPosition);
        if (copyArea){
            if (sideObj.sideShow === false){
                sideObj.sideShow = true; //
                this.setState({ sideObj });
            }
        }
    }

    //
    handleAddGrandson = (e, position) => {
        e.stopPropagation();
        this.addArea(this.props, {
            items: {
                areatype: '2',
                relationcode: this.props.areaItem.code,
                isgrandson: true
            },
            position
        });
    }

    //className area areaClassNameclassName
    getAreaItemCls = (area, areaClassName = "area-item") => {
        if(!area) return;
        const { selectedArea, selectCard } = this.props;
        //
        if (selectedArea && selectedArea.pk_area === area.pk_area){
            areaClassName += " active";
        } else if (selectCard && selectCard.areaid === area.pk_area){ //
            areaClassName += " card-active";
        }
        return areaClassName;
    }
    
    /**
     * 
     *
     * @type  - area group
     * @params 
     */
    renderHeaderBtns = (type = 'area', params = {}) => {
        let { json, index, isImplement, areaItem, zoneFormData } = this.props;
        const implementHideBtns = [
            'onAddFormGroup', 
            'onCopySideArea',
            'onCopyFoldArea',
            'onMoveDown',
            'onMoveUp',
            'onAddGrandsonTable',
            'onDelete'
        ];
        //
        if (isImplement) {
            implementHideBtns.forEach(btn => delete params[btn]);
        }
        const buttonCls = `opr-btn ${type}-btn`;
        let {
            child,                  //
            hasMeta,                //
            onHideGray,             //
            onAddMeta,              //
            onBatchSetting,         //
            onAddFormGroup,         //
            onCopySideArea,         //
            onCopyFoldArea,         //
            onMoveDown,             //
            onMoveUp,               //
            onAddGrandsonTable,     //
            onDelete,               //
        } = params;
        // console.log({params})
        const isFirstArea = this.checkAreaIsBeginOrEnd({ type: 'begin', index, child }); //
        const isLastArea = this.checkAreaIsBeginOrEnd({ type: 'end', index, child }); //
        if (child !== undefined) index = this.getAreaIndexByPk(child);
        return (
            <span className="area-item-button">
                <Checkbox onChange={onHideGray} >
                    <span className="link">{langCheck('ZoneSetting-000013', 'pages', json)}</span>
                    {/* /*  */}
                </Checkbox>
                {
                    hasMeta() ? 
                        <span><a onClick={onAddMeta}>{langCheck('ZoneSetting-000014', 'pages', json)}</a></span> /*  */
                        : 
                        <Tooltip placement="top" title={langCheck('ZoneSetting-000203', 'pages', json)}> {/* */}
                            <a style={{color: '#b9b9b9'}}>{langCheck('ZoneSetting-000014', 'pages', json)}</a> {/*  */}
                        </Tooltip>
                }
                {/* <a onClick={this.openAddNotMetaInArea}>{langCheck('ZoneSetting-000004', 'pages', json)}</a> */}
                {/* /*  */}
                <a onClick={onBatchSetting}>{langCheck('ZoneSetting-000015', 'pages', json)}</a>
                {/* /*  */}
                {/*  */}
                {
                    areaItem.code === (zoneFormData && zoneFormData.headcode) && onAddFormGroup && 
                    <span className={buttonCls} onClick={onAddFormGroup} style={{marginRight: 8}}>
                        <i
                            title={langCheck('ZoneSetting-000209', 'pages', json)} /*  */
                            className="iconfont icon-xinzengfenzu"
                        />
                    </span>
                }
                {
                    (onCopySideArea && onCopyFoldArea) && 
                    <span className="btn-group body-btns" style={{ marginRight: 8 }}>
                        {/*  */}
                        {
                            areaItem.code && onCopySideArea ?
                                <span className={buttonCls} onClick={onCopySideArea}>
                                    <i className="iconfont icon-fuzhicela" title={langCheck('ZoneSetting-000211', 'pages', json)} /> {/*  */}
                                </span>
                                :
                                <Tooltip className={buttonCls} placement="top" title={langCheck('ZoneSetting-000221', 'pages', json)}> {/* */}
                                    <span onClick={onCopySideArea}>
                                        <i className="iconfont icon-fuzhicela disabled" title={langCheck('ZoneSetting-000211', 'pages', json)} /> {/*  */}
                                    </span>
                                </Tooltip>
                        }
                        {/*  */}
                        {
                            areaItem.code && onCopyFoldArea ?
                                <span className={buttonCls} onClick={onCopyFoldArea}>
                                    <i className="iconfont icon-fuzhikapian" title={langCheck('ZoneSetting-000212', 'pages', json)} /> {/*  */}
                                </span>
                                :
                                <Tooltip className={buttonCls} placement="top" title={langCheck('ZoneSetting-000221', 'pages', json)}> {/* */}
                                    <span onClick={onCopyFoldArea}>
                                        <i className="iconfont icon-fuzhikapian disabled" title={langCheck('ZoneSetting-000212', 'pages', json)} /> {/*  */}
                                    </span>
                                </Tooltip>
                        }
                    </span>
                }
                <span className="btn-group">
                    {
                        /*  */
                        onMoveDown && <span className={buttonCls} onClick={!isLastArea && onMoveDown}>
                            <i
                                title={langCheck('ZoneSetting-000189', 'pages', json)}
                                className={`iconfont icon-xiangxia ${isLastArea ? 'disabled' : ''}`}
                            />
                        </span>
                    }
                    
                    {
                        /*  */
                        onMoveUp && <span className={buttonCls} onClick={!isFirstArea && onMoveUp} >
                            <i
                                title={langCheck('ZoneSetting-000188', 'pages', json)}
                                className={`iconfont icon-xiangshang ${isFirstArea ? 'disabled' : ''}`}
                            />
                        </span>
                    }
                    {
                        /*  */
                        onAddGrandsonTable && <span className={buttonCls} onClick={onAddGrandsonTable} >
                            <i
                                title={langCheck('ZoneSetting-000218', 'pages', json)}
                                className="iconfont icon-xinzengsunbiao"
                            />
                        </span>
                    }
                    {/*  */}
                    {
                        onDelete && 
                        <Popconfirm
                            title={langCheck('ZoneSetting-000191', 'pages', json)} /*  */
                            onConfirm={onDelete}
                            placement='topRight'
                            getPopupContainer={() => document.getElementsByClassName("template-setting-page")[0]}
                            okText={langCheck('ZoneSetting-000005', 'pages', json)}
                            cancelText={langCheck('ZoneSetting-000006', 'pages', json)}
                        >
                            <span className={buttonCls}>
                                <i className="iconfont icon-shanchu" title={langCheck('ZoneSetting-000190', 'pages', json)} />
                            </span>
                        </Popconfirm>
                    }
                </span>
            </span>
        )
    }
    //
    renderHeader = (type = 'area', params = {}) => {
        const { json, areaItem } = this.props;
        const {
            headerTitle = areaItem.name, //
            headerVisible = true,
            contentVisible,
            onToggleHeader,
            onDrawerToggle, //
            btnContainerStyle //
        } = params;
        const toggleBtn = contentVisible ? (
            <i
                title={langCheck('ZoneSetting-000011', 'pages', json)}/*  */
                className="iconfont icon-bottom"
            ></i>
        ) : (
                <i
                    title={langCheck('ZoneSetting-000012', 'pages', json)}/*  */
                    className="iconfont icon-right"
                ></i>

            )
        return <div className={`area-item-header ${headerVisible ? 'show' : 'hide'}`}>
            <div className="area-item-name-wrapper" onClick={onToggleHeader}>
                <span className="area-item-name">
                    {onToggleHeader && toggleBtn}
                    {/*  */}
                    <span className="area-name">{headerTitle}</span>
                </span>
            </div>
            <div className="area-item-button-wrapper" style={btnContainerStyle}>
                {this.renderHeaderBtns(type, params)}
            </div>
            {
                onDrawerToggle && <span className="drawer-close-btn" onClick={onDrawerToggle}>
                    <i className="iconfont icon-celashouqi"></i>
                </span>
            }
        </div>
    };

    //
    renderFormGroupCard = (item, groupIndex, cardIndex) => {
        let { shouldHideGray, groupNotVisibleArr } = this.state;
        const { areaItem } = this.props;
        let group = areaItem.groups && areaItem.groups[groupIndex];
        let areaIndex = this.getAreaIndexByPk(group);
        shouldHideGray = groupNotVisibleArr[groupIndex];
        return this.renderCard({
            item, 
            cardIndex, 
            areaType: 'form', 
            areaIndex,
            shouldHideGray
        });
    }

    //
    renderTableFoldCard = (item, foldIndex, cardIndex) => {
        let fold = this.props.areaItem.sides && this.props.areaItem.sides[foldIndex];
        let areaIndex = this.getAreaIndexByPk(fold);
        return this.renderCard({
            item,
            cardIndex,
            areaType: 'form',
            areaIndex,
            shouldHideGray: this.state.foldObj.foldNotVisible
        });
    }

    //
    renderGrandsonCard = (item, grandsonIndex, cardIndex) => {
        let grandson = this.props.areaItem.grandsons && this.props.areaItem.grandsons[grandsonIndex];
        let areaIndex = this.getAreaIndexByPk(grandson);
        return this.renderCard({
            item,
            cardIndex,
            areaType: 'table',
            areaIndex,
            shouldHideGray: this.state.grandsonObj.grandsonNotVisibleArr[grandsonIndex]
        });
    }

    /**
     * card
     *
     * @item - queryPropertyListitem
     * @areaItem - item
     * @areaIndex - index
     * @cardIndex - queryPropertyListindex
     * @areaType -  search: card: table:
     */
    renderCard = ({
        item, 
        areaIndex = this.props.index,
        cardIndex, 
        areaType,
        shouldHideGray = this.state.shouldHideGray
    }) => (
        <MyCard
            index={cardIndex}
            areaItem={this.props.areaItem}
            areaIndex={areaIndex}
            key={cardIndex}
            shouldHideGray={shouldHideGray}
            id={item.pk_query_property}
            name={item.label}
            areaid={this.props.areaItem.pk_area}
            visible={item.visible}
            color={item.color}
            required={item.required}
            // selectThisCard={this.selectThisCard}
            moveCard={this.moveCard}
            deleteCard={(cardIndex) => this.deleteCard(cardIndex, areaIndex)}
            cardInfo={item}
            areaType={areaType}
            json={this.props.json}
            isImplement={this.props.isImplement}
        />
    )

    //
    renderSearchArea = () => (
        <ul className="area-item-content" style={{ display: this.props.isShow ? "block" : "none" }}>
            <div className="search-area-content">
                <div className="query-btn-area">
                    <Button className='ant-btn-primary'>
                        <i className="iconfont icon-sousuo" />
                    </Button>
                </div>
                {this.props.areaItem.queryPropertyList.map(
                    (item, index) => this.renderCard({
                        item,
                        cardIndex: index,
                        areaType: 'search'
                    })
                )}
            </div>
        </ul>
    )

    //
    renderGroupHeader = (group, groupIndex, btns) => {
        const contentVisible = this.getGroupContentVisible(groupIndex);
        return <div className="form-group-header">
            <span className="form-group-title">
                <span className="group-name">{group.name}</span>
                <i 
                    className={`iconfont ${contentVisible ? 'icon-shouqi' : 'icon-zhankai'}`} 
                    onClick={() => this.toggleFormGroup(groupIndex)}
                ></i>
            </span>
            <div className="dashed-line"></div>
            <div className="group-btns">{btns}</div>
        </div>
    }
    
    //
    renderAreaHeader = () => {
        let { areaItem, areatype, id, index, onMoveArea } = this.props;
        //
        const copyFold = e => {
            e.stopPropagation();
            if (!this.props.areaItem.code) return;
            this.handleCopyFoldArea(index + 1);
        }
        //
        const copySide = e => {
            e.stopPropagation();
            if(!this.props.areaItem.code) return;
            this.handleCopySideArea(index + 1);
        }
        return this.renderHeader('area', {
            headerVisible: true,
            contentVisible: this.props.isShow,
            onToggleHeader: this.showOrHideAreaItem,
            hasMeta: () => areaItem.metaid,
            btnContainerStyle: areatype === '2' ? { width: 485 } : {},
            onHideGray: this.handleHideGray,
            onAddMeta: () => this.addMetaInArea(),
            onBatchSetting: () => this.openBatchSetting(),
            onAddFormGroup: areatype === '1' && this.handleAddFormGroup,     //
            onCopySideArea: areatype === '2' && copySide,   //   
            onCopyFoldArea: areatype === '2' && copyFold,   //
            onMoveDown: () => onMoveArea('down', index),
            onMoveUp: () => onMoveArea('up', index),
            onDelete: () => this.handleDeleteArea(id)
        });
    }

    //
    renderFormGroup = (group, groupIndex) => {
        const isActive = this.props.selectedArea && this.props.selectedArea.pk_area === group.pk_area;
        const groupAreaIndex = this.getAreaIndexByPk(group); //id
        let { groupShowArr } = this.state;
        const groupHeaderBtns = this.renderHeaderBtns('group', {
            child: { groups: group},
            headerVisible: isActive,
            hasMeta: () => group.metaid,
            onToggleHeader: () => this.toggleFormGroup(groupIndex),
            onHideGray: (e) => this.handleGroupHideGray(e, groupIndex),
            onAddMeta: () => this.addMetaInArea({
                metaId: group.metaid,
                areaId: group.pk_area,
                areaType: group.areatype
            }),
            onBatchSetting: () => this.openBatchSetting(groupAreaIndex),
            onAddFormGroup: (e) => this.handleAddFormGroup(e, groupAreaIndex + 1),
            onMoveDown: () => this.props.onMoveFormGroup('down', groupAreaIndex),
            onMoveUp: () => this.props.onMoveFormGroup('up', groupAreaIndex),
            onDelete: () => this.props.onDeleteArea(group.pk_area)
        });
        const groupHeader = this.renderGroupHeader(group, groupIndex, groupHeaderBtns);
        return <div ref={ref => this[`formGroupRef${groupIndex}`] = ref}>
            <FormGroupArea
                group={group}
                groupIndex={groupIndex}
                groupShowArr={groupShowArr}
                isActive={isActive}
                contentVisible={this.getGroupContentVisible(groupIndex)}
                renderHeader={groupHeader}
                renderCard={(item, index) => this.renderFormGroupCard(item, groupIndex, index)}
                emptyText={langCheck('ZoneSetting-000226', 'pages', this.props.json)} /*  */
                onClick={e => this.selectAreaItem(e, group)}
                onDrop={(monitor) => this.handleSpecialAreaDrop(monitor, groupAreaIndex)}
            />
        </div>
    }

    //
    renderFormArea = () => {
        const { areaItem } = this.props;
        return <ul className="area-item-content" style={{ display: this.props.isShow ? "block" : "none" }}>
                <div className="form-area-content">
                {areaItem.queryPropertyList.map(
                    (item, index) => this.renderCard({
                        item,
                        cardIndex: index,
                        areaType: 'form'
                    })
                )}
                </div>
                {
                    areaItem.groups && areaItem.groups.map((group, index) => {
                        return this.renderFormGroup(group, index);
                    })
                }
            </ul>
        
    }

    // onlyGrandson:
    renderTableSide = (side, sideIndex, onlyGrandson) => {
        const { sideShow } = this.state.sideObj;
        const isActive = this.props.selectedArea && this.props.selectedArea.pk_area === side.pk_area;
        const sideAreaIndex = !onlyGrandson ? this.getAreaIndexByPk(side) : sideIndex; //id
        const sideHeader = this.renderHeader('area', {
            headerTitle: side.name,
            hasMeta: () => side.metaid,
            btnContainerStyle: { width: 380 },
            onHideGray: this.handleFoldHideGray,
            onAddMeta: () => this.addMetaInArea({
                metaId: side.metaid,
                areaId: side.pk_area,
                areaType: side.areatype
            }),
            onBatchSetting: () => this.openBatchSetting(sideAreaIndex),
            onAddGrandsonTable: this.handleAddGrandson,
            onDelete: () => {
                this.handleDeleteArea(side.pk_area)
                    .then(() => {
                        let { sideObj } = this.state;
                        sideObj.sideShow = false; //
                        this.setState({ sideObj });
                    });
            },
            onDrawerToggle: this.handleToggleSideForm
        });
        const sideCard = (item, index) => this.renderTableFoldCard(item, sideIndex, index);
        return (
            <TableDrawerArea
                areaClassName={this.getAreaItemCls(side)}
                side={side}
                isActive={isActive}
                sideIndex={sideIndex}
                renderHeader={sideHeader}
                renderCard={sideCard}
                visible={sideShow}
                onClick={e => this.selectAreaItem(e, side)}
                onDrop={(monitor) => this.handleSpecialAreaDrop(monitor, sideAreaIndex)}
            >
                {/*  */}
                {() => (
                    <div className="grandson-content">
                        {
                            this.props.areaItem.grandsons && this.props.areaItem.grandsons.map((grandson, index) => {
                                return this.renderGrandsonArea(grandson, index);
                            })
                        }
                    </div>
                )}
            </TableDrawerArea>
        )
    }

    //
    renderTableFold = (fold, foldIndex) => {
        const { foldShow } = this.state.foldObj;
        const isActive = this.props.selectedArea && this.props.selectedArea.pk_area === fold.pk_area;
        const foldAreaIndex = this.getAreaIndexByPk(fold); //id
        const foldHeader = this.renderHeader('area', {
            headerTitle: fold.name,
            contentVisible: foldShow,
            btnContainerStyle: { width: 355 },
            hasMeta: () => fold.metaid,
            onToggleHeader: this.handleToggleFoldForm,
            onHideGray: this.handleFoldHideGray,
            onAddMeta: () => this.addMetaInArea({
                metaId: fold.metaid,
                areaId: fold.pk_area,
                areaType: fold.areatype
            }),
            onBatchSetting: () => this.openBatchSetting(foldAreaIndex),
            onDelete: () => {
                this.handleDeleteArea(fold.pk_area)
                    .then(() => {
                        let { foldObj } = this.state;
                        foldObj.foldShow = false; //
                        this.setState({ foldObj });
                    });
            }
        });
        const foldCard = (item, index) => this.renderTableFoldCard(item, foldIndex, index);
        const foldCls = this.getAreaItemCls(fold, "");
        return <div className={`table-fold-area ${foldCls}`}>
            <TableFoldArea
                fold={fold}
                foldIndex={foldIndex}
                isActive={isActive}
                renderHeader={foldHeader}
                renderCard={foldCard}
                visible={foldShow}
                onClick={e => this.selectAreaItem(e, fold)}
                onDrop={(monitor) => this.handleSpecialAreaDrop(monitor, foldAreaIndex)}
            />
        </div>
    }

    //
    renderGrandsonArea = (grandson, grandsonIndex) => {
        const isActive = this.props.selectedArea && this.props.selectedArea.pk_area === grandson.pk_area;
        const grandsonAreaIndex = this.getAreaIndexByPk(grandson); //index
        const contentVisible = this.getGrandsonContentVisible(grandsonIndex);
        const grandsonHeader = this.renderHeader('area', {
            child: { grandsons:grandson },
            headerTitle: grandson.name,
            hasMeta: () => grandson.metaid,
            contentVisible,
            onToggleHeader: () => this.handleToggleGrandson(grandsonIndex),
            onHideGray: (e) => this.handleGrandsonHideGray(e, grandsonIndex),
            onAddMeta: () => this.addMetaInArea({
                metaId: grandson.metaid,
                areaId: grandson.pk_area,
                areaType: grandson.areatype
            }),
            onBatchSetting: () => this.openBatchSetting(grandsonAreaIndex),
            onAddGrandsonTable: (e) => this.handleAddGrandson(e, grandsonAreaIndex + 1),
            onMoveDown: () => this.props.onMoveGrandson('down', grandsonAreaIndex, grandson),
            onMoveUp: () => this.props.onMoveGrandson('up', grandsonAreaIndex, grandson),
            onDelete: () => this.handleDeleteArea(grandson.pk_area)
        });
        const grandsonCard = (item, index) => this.renderGrandsonCard(item, grandsonIndex, index);
        
        return <GrandsonArea
                areaClassName={this.getAreaItemCls(grandson)}
                grandson={grandson}
                grandsonIndex={grandsonAreaIndex}
                isActive={isActive}
                contentVisible={contentVisible}
                renderHeader={grandsonHeader}
                renderCard={grandsonCard}
                isImplement={this.props.isImplement}
                onClick={e => this.selectAreaItem(e, grandson)}
                onDrop={(monitor) => this.handleSpecialAreaDrop(monitor, grandsonAreaIndex)}
                onDragArea={this.props.onDragArea}
            />
    }

    //
    renderTableArea = () => {
        const { areaItem } = this.props;
        let drawerArea = areaItem.sides && [...areaItem.sides];
        if (areaItem.grandsons){
            drawerArea = [...drawerArea, ...areaItem.grandsons];
        }
        return <ul className="area-item-content" style={{ display: this.props.isShow ? "block" : "none" }}>
            <div className="table-area-content" style={this.hideTableOprCol() ? { marginRight: 0 } : { marginRight: this.state.oprWidth}}>
                {this.renderTableOperateCol()}
                {this.props.areaItem.queryPropertyList.map(
                    (item, index) => this.renderCard({
                        item,
                        cardIndex: index,
                        areaType: 'table'
                    })
                )}
            </div>
            {
                drawerArea && drawerArea.map((side, index) => {
                    const sideArea =  drawerArea.find(item => item.areastatus === 'edit' && item.isgrandson !== true);
                    //
                    if (side.isgrandson === true){
                        if (!sideArea){
                            return this.renderTableSide(areaItem, this.props.index, true);
                        }
                    }else{
                        if (side.areastatus === 'browse') { //
                            return this.renderTableFold(side, index);
                        } else if (side.areastatus === 'edit'){ //
                            return this.renderTableSide(side, index);
                        }
                    }
                })
            }
        </ul>
    }
    
    //
    renderTableOperateCol = () => {
        const { areaItem, json, isImplement } = this.props;
        const foldText = this.state.foldObj.foldShow ? 
            langCheck('ZoneSetting-000217', 'pages', json) : /*  */
            langCheck('ZoneSetting-000215', 'pages', json);  /*  */
        let renderSide = () => {
            //
            if(isImplement && (areaItem.sides && !areaItem.sides.find(item => item.areastatus === 'edit')) && (!areaItem.grandsons || areaItem.grandsons.length === 0)){
                return null;
            }else if(areaItem.code){
                return <span className="btn-link" style={{ marginRight: 20 }} onClick={this.handleToggleSideForm}>
                    {/*  */}
                    {langCheck('ZoneSetting-000216', 'pages', json)}
                </span>
            }else{
                return <Tooltip placement="top" title={langCheck('ZoneSetting-000221', 'pages', json)}> {/* */}
                    <span className="btn-link disabled" style={{ marginRight: 20 }}>
                        {/*  */}
                        {langCheck('ZoneSetting-000216', 'pages', json)}
                    </span>
                </Tooltip>
            }
        }
        let renderFold = () => {
            //
            if(isImplement && (areaItem.sides && !areaItem.sides.find(item => item.areastatus === 'browse'))){
                return null;
            }else if(areaItem.code){
                return <span className="btn-link" onClick={this.handleToggleFoldForm}>{foldText}</span>; /*  */
            }else{
                return <Tooltip placement="top" title={langCheck('ZoneSetting-000221', 'pages', json)}> {/* */}
                        <span className="btn-link disabled">{foldText}</span>
                    </Tooltip>
            }
        }
        ///
        if(this.hideTableOprCol()){
            return null;
        }
        return <div className="operate-col" ref={ref => this.oprColRef = ref}>
            <li className="property-item table-content normal-card">
                <div className="table-item">
                    <div className="item-name">
                        <span className="title">{langCheck('ZoneSetting-000225', 'pages', json)}</span> {/*  */}
                    </div>
                    <div className="item-content oper-col-content">
                        {renderSide()}
                        {renderFold()}
                    </div>
                </div>
            </li>
        </div>
    }

    //
    hideTableOprCol = () => {
        const { areaItem, isImplement } = this.props;
        //
        if(areaItem.grandsons && areaItem.grandsons.length > 0){
            return false;
        }else{
            return isImplement && (!areaItem.sides || areaItem.sides && areaItem.sides.length === 0);
        }
    }
    //
    renderAreaContent = () => {
        let areaContent;
        switch (this.props.areaItem.areatype) {
            case '0': //
                areaContent = this.renderSearchArea();
                break;
            case '1': //
                areaContent = this.renderFormArea();
                break;
            case '2': //
                areaContent = this.renderTableArea();
                break;
            default:
                break;
        }
        return areaContent;
    }   
    render() {
        const { connectDragSource, connectDropTarget, areaList, index } = this.props;
        let areaClassName = this.getAreaItemCls(this.props.areaItem);
        const areaStyle = areaList[index] && areaList[index].validate === false ? { borderColor: '#f5222d'} : {};
        return connectDragSource(
                connectDropTarget(
                    <div className={areaClassName} onClick={this.selectAreaItem} style={areaStyle}>
                        {this.renderAreaHeader()}
                        {this.renderAreaContent()}
                        {
                            this.state.batchSettingModalVisibel && 
                            <BatchSettingModal
                                batchSettingModalVisibel={
                                    this.state.batchSettingModalVisibel
                                }
                                areaIndex={this.state.realAreaIndex}
                                setModalVisibel={this.setModalVisibel}
                                json={this.props.json}
                            />
                        }
                        <AddNotMetaDataModal
                            areatype={this.props.areatype}
                            addDataModalVisibel={this.state.addDataModalVisibel}
                            areaIndex={this.state.realAreaIndex}
                            setAddDataModalVisibel={this.setAddDataModalVisibel}
                            json={this.props.json}
                            propperty={this.state.currentNotMeta}
                        />
                    </div>
                )
            );
    }
}
export default connect(
    state => ({
        selectedArea: state.zoneSettingData.selectedArea,
        areaList: state.zoneSettingData.areaList,
        newAreaList: state.zoneSettingData.newAreaList,
        selectCard: state.zoneSettingData.selectCard,
        zoneFormData: state.zoneSettingData.zoneFormData
    }),
    {
        updateSelectCard,
        updateSelectArea,
        updateBatchSource,
        updateAreaList
    }
)(AreaItem);
