import React, { Component } from 'react';
import { connect } from 'react-redux';
import { DragSource } from 'react-dnd';

const dragItemSource = {
    beginDrag(props) {
        return {
            type: 'card',
            from: 'left',
            datatype: props.items.value
        };
    },

};

const DragItem = ({ items, connectDragSource, onDragItemClick }) => {
    const { datatype, name, icon, nowrap } = items;
    return connectDragSource(
        <div className="drag-item-wrapper" key={datatype} onClick={onDragItemClick} style={nowrap && {padding: '6px 2px'}}>
            <div className="drag-item" title={name}>
                <i className={`iconfont icon-${icon}`}/>
                <span className="name">{name}</span>
            </div>
        </div>
    ) 
}

export default DragSource('area', dragItemSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    connectDragPreview: connect.dragPreview(),
    isDragging: monitor.isDragging()
}))(DragItem)