import React, { Component } from 'react';
import { DropTarget } from 'react-dnd';
import { Drawer } from 'antd';

const areaTarget = {
    drop(props, monitor, component){
        const dragItem = monitor.getItem();
        //
        if (dragItem.type === 'card') {
            props.onDrop(monitor);
        }
    }
};

//DropTarget
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver()
}))
class TableDrawerArea extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        const { 
            areaClassName,
            side, 
            children,
            isActive,
            renderCard,
            visible,
            renderHeader,
            onClick,
            connectDropTarget
        } = this.props;
        return <Drawer
            getContainer={document.getElementsByClassName("template-setting-content")[0]}
            closable={false}
            placement="right"
            maskClosable={false}
            width={725}
            visible={visible}
            mask={true}
            zIndex={180}
            destroyOnClose={true}
            style={{ padding: 0, height: '100%'}}
        >
        {
            <div className="drawer-content">
                <div className={`${areaClassName} side-area`} onClick={onClick}>
                    <div className="table-side-area">
                        {renderHeader}
                        {connectDropTarget(
                            <div className="area-item-content">
                                <div className={`form-area-group ${isActive ? 'active' : ''}`}>
                                    <div className="form-area-content">
                                        {side.queryPropertyList.map(renderCard)}
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
                {/*  */}
                {
                    children && children()
                }                    
            </div>
        }
        </Drawer>
    }
}

export default TableDrawerArea;