import React, { Component } from "react";
import _ from "lodash";
import Ajax from "Pub/js/ajax";
import { connect } from "react-redux";
import { Tree, Button, Checkbox, Select, Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import * as utilService from "./utilService";
import { opersignNameObj, colorObj } from "./utilService";
const TreeNode = Tree.TreeNode;
const Option = Select.Option;
import { langCheck } from "Pub/js/utils";
import { selectCardFromOther } from "./events";
import { updateSelectCard, updateSelectArea } from "Store/ZoneSetting/action";
//
class TreeModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            checkedKeys: [],
            selectedKeys: [],
            selectedNodes: [],
            searchValue: "",
            needToBeAllSelectNodeList: []
        };
    }
    //
    showModalHidden = () => {
        this.setModalVisible(false);
    };
    //
    setModalVisible = modalVisible => {
        this.setState({
            selectedKeys: [],
            checkedKeys: [],
            selectedNodes: [],
            checkedNodes: [],
            needToBeAllSelectNodeList: []
        });
        this.props.setModalVisible(modalVisible);
    };
    //
    onOkMoveDialog = () => {
        const modalVisible = false;
        const { selectedNodes } = this.state;
        const { targetAreaID, json } = this.props;
        let cardList = [];
        ///
        _.forEach(selectedNodes, (s, i) => {
            const {
                myUniqID,
                datatype,
                refname,
                refcode,
                pid,
                refpk,
                isLeaf,
                modelname,
                length,
                notSearchAreaCode,
                proid,
                resid,
                readonly
            } = s.props.dataRef;
            let cardObj = {};
            //
            if (this.props.targetAreaType === "0") {
                //
                const opersign = utilService.getObjNameByDatatype(
                    datatype,
                    utilService.opersignObj
                );
                //
                const opersignname = utilService.getObjNameByDatatype(
                    datatype,
                    opersignNameObj()
                );
                cardObj = {
                    pk_query_property: `newMetaData_${myUniqID}`,
                    areaid: targetAreaID,
                    datatype: datatype,
                    code: myUniqID,
                    label: refname,
                    metapath: myUniqID,
                    isnotmeta: false,
                    isuse: true,
                    opersign: opersign ? opersign : "=@>@>=@<@<=@like@",
                    opersignname: opersignname
                        ? opersignname
                        : langCheck(
                              "ZoneSetting-000001",
                              "pages",
                              json
                          ) /*  @@@@@@*/,
                    defaultvalue: "",
                    isfixedcondition: false,
                    required: false,
                    disabled: false,
                    visible: datatype === "56" ? false : true,
                    ismultiselectedenabled: false,
                    isquerycondition: true,
                    refname: datatype === "204" ? modelname : "-99",
                    containlower: isLeaf ? false : true,
                    ischeck: false,
                    isbeyondorg: false,
                    usefunc: datatype === "34" ? true : false,
                    showtype: "1",
                    returntype: "refpk",
                    define1: "",
                    define2: "",
                    define3: "",
                    define4: "",
                    define5: "",
                    itemtype: "input",
                    myrefpk: refpk,
                    visibleposition: "",
                    tablecode: "",
                    querytablename: "",
                    maxlength: length //
                };
            } else {
                //
                cardObj = {
                    pk_query_property: `newMetaData_${myUniqID}`,
                    areaid: targetAreaID,
                    code: notSearchAreaCode ? notSearchAreaCode : myUniqID,
                    datatype: datatype,
                    label: refname,
                    metapath: myUniqID,
                    color: colorObj() && colorObj()[0].value, //
                    isrevise: false,
                    required: false,
                    disabled: false,
                    visible: datatype === "56" ? false : true,
                    maxlength: length,
                    defaultvalue: "",
                    hyperlinkflag: false, //
                    defaultvar: "",
                    define1: "",
                    define2: "",
                    define3: "",
                    itemtype: "input",
                    myrefpk: refpk
                };
            }
            if (this.props.targetAreaType === "1") {
                //
                cardObj.colnum = "1";
                cardObj.isnextrow = false;
            }
            if (this.props.targetAreaType === "2") {
                //
                cardObj.width = "";
                cardObj.istotal = false;
            }
            //
            if (cardObj.datatype === "204") {
                cardObj.metaid = refpk;
                cardObj.iscode = false;
                cardObj.modelname = modelname;
            }
            //
            if (cardObj.datatype === "56") {
                cardObj.isdefined = true;
            }
            //
            if (cardObj.datatype === "31" || cardObj.datatype === "52") {
                cardObj.dataval = "2,,";
            }
            //
            cardObj.itemtype = utilService.getItemtypeByDatatype(
                cardObj.datatype
            );
            //false
            // if (this.props.targetAreaType === "0" && _.includes(utilService.shouldSetDefaultValueList, cardObj.itemtype)) {
            //     cardObj.defaultvalue='false'
            // }
            //
            cardObj.proid = proid;
            cardObj.resid = resid;
            if (readonly === true) {
                cardObj.disabled = true;
            }
            cardList.push(cardObj);
        });
        //&dataval
        let ajaxDataForSelect = [];
        _.forEach(cardList, c => {
            if (c.datatype === "203") {
                console.log(c.label);
                ajaxDataForSelect.push(c.myrefpk);
            }
        });
        //
        if (ajaxDataForSelect.length > 0) {
            Ajax({
                url: `/nccloud/platform/templet/getEnumValue.do`,
                info: {
                    name: langCheck(
                        "ZoneSetting-000017",
                        "pages",
                        json
                    ) /*  */,
                    action: langCheck(
                        "ZoneSetting-000122",
                        "pages",
                        json
                    ) /*  */
                },
                data: ajaxDataForSelect,
                success: res => {
                    if (res) {
                        let { data, success } = res.data;
                        if (success && data) {
                            _.forEach(data, d => {
                                _.forEach(cardList, c => {
                                    _.forEach(d, d2 => {
                                        if (d2.id === c.myrefpk) {
                                            if (_.isEmpty(c.dataval)) {
                                                c.dataval = `${d2.name}=${
                                                    d2.value
                                                }`;
                                            } else {
                                                c.dataval = `${c.dataval},${
                                                    d2.name
                                                }=${d2.value}`;
                                            }
                                        }
                                    });
                                });
                            });
                        }
                        this.setDatavalForCard(cardList, modalVisible);
                    }
                }
            });
        } else {
            this.setDatavalForCard(cardList, modalVisible);
        }
    };
    //dataval
    setDatavalForCard = (cardList, modalVisible) => {
        let { json } = this.props;
        if (this.props.targetAreaType !== "0") {
            let ajaxData = [];
            _.forEach(cardList, c => {
                if (c.datatype === "204") {
                    ajaxData.push(c.modelname);
                }
            });
            if (ajaxData.length > 0) {
                Ajax({
                    url: `/nccloud/platform/templet/getRefDefaultSel.do`,
                    info: {
                        name: langCheck(
                            "ZoneSetting-000017",
                            "pages",
                            json
                        ) /*  */,
                        action: langCheck(
                            "ZoneSetting-000024",
                            "pages",
                            json
                        ) /*  */
                    },
                    data: ajaxData,
                    success: res => {
                        if (res) {
                            let { data, success } = res.data;
                            if (success && data) {
                                _.forEach(data, d => {
                                    _.forEach(cardList, c => {
                                        if (d.name === c.modelname) {
                                            c.dataval = `${
                                                d.pk_refinfo
                                            },code=N`;
                                            c.refname = d.name;
                                            c.refcode = d.refpath;
                                        }
                                    });
                                });
                            }
                            cardList[0] &&
                                selectCardFromOther.call(this, cardList[0]);
                            this.props.addCard(cardList);
                            this.setModalVisible(modalVisible);
                        }
                    }
                });
            } else {
                cardList[0] && selectCardFromOther.call(this, cardList[0]);
                this.props.addCard(cardList);
                this.setModalVisible(modalVisible);
            }
        } else {
            cardList[0] && selectCardFromOther.call(this, cardList[0]);
            this.props.addCard(cardList);
            this.setModalVisible(modalVisible);
        }
    };
    //
    //selectKeysSelectNodes
    onSelect = (selectedKeys, info) => {
        // this.setState({ selectedKeys, selectedNodes: info.selectedNodes });
        this.setState({
            checkedKeys: selectedKeys,
            selectedKeys: selectedKeys,
            selectedNodes: info.selectedNodes,
            checkedNodes: info.selectedNodes
        });
    };
    //checkedKeys
    onCheck = (checkedKeys, info) => {
        // this.setState({
        //     checkedKeys,
        //     checkedNodes: info.checkedNodes
        // });
        this.setState({
            selectedKeys: checkedKeys.checked,
            checkedKeys: checkedKeys.checked,
            checkedNodes: info.checkedNodes,
            selectedNodes: info.checkedNodes //checkedbox
        });
    };
    //
    renderTreeNodes = data => {
        return data.map(item => {
            if (item.children) {
                return (
                    <TreeNode
                        title={item.title}
                        switcherIcon={({ expanded }) => {
                            return (
                                <i
                                    className={`font-size-18 iconfont ${
                                        expanded
                                            ? "icon-shu_zk"
                                            : "icon-shushouqi"
                                    }`}
                                />
                            );
                        }}
                        key={item.key}
                        dataRef={item}
                    >
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            if (item.isleaf) {
                return <TreeNode {...item} dataRef={item} />;
            } else {
                return (
                    <TreeNode
                        switcherIcon={({ expanded }) => {
                            return (
                                <i
                                    className={`font-size-18 iconfont ${
                                        expanded
                                            ? "icon-shu_zk"
                                            : "icon-shushouqi"
                                    }`}
                                />
                            );
                        }}
                        {...item}
                        dataRef={item}
                    />
                );
            }
        });
    };
    //ajax
    onLoadData = treeNode => {
        let { json } = this.props;
        return new Promise(resolve => {
            if (treeNode.props.children) {
                resolve();
                return;
            }
            Ajax({
                url: `/nccloud/platform/templet/querymetapro.do`,
                info: {
                    name: langCheck(
                        "ZoneSetting-000017",
                        "pages",
                        json
                    ) /*  */,
                    action: langCheck(
                        "ZoneSetting-000020",
                        "pages",
                        json
                    ) /*  */
                },
                data: {
                    // metaid: metaid
                    metaid: treeNode.props.refpk
                },
                success: res => {
                    if (res) {
                        let { data, success } = res.data;
                        if (
                            success &&
                            data &&
                            data.rows &&
                            data.rows.length > 0
                        ) {
                            let metaTree = [];
                            data.rows.map((r, index) => {
                                let tmpMetaTree = {
                                    ...r,
                                    title: `${r.refcode} ${r.refname}`,
                                    key: `${treeNode.props.myUniqID}.${
                                        r.refcode
                                    }`,
                                    myUniqID: `${treeNode.props.myUniqID}.${
                                        r.refcode
                                    }`,
                                    isLeaf: r.isleaf
                                };
                                //code
                                if (
                                    this.props.targetAreaType !== "0" &&
                                    treeNode.props.notSearchAreaCode
                                ) {
                                    tmpMetaTree.notSearchAreaCode = `${
                                        treeNode.props.notSearchAreaCode
                                    }.${r.refcode}`;
                                }
                                //code205refcode
                                if (
                                    this.props.targetAreaType !== "0" &&
                                    treeNode.props.datatype === "205"
                                ) {
                                    tmpMetaTree.notSearchAreaCode = `${
                                        r.refcode
                                    }`;
                                }
                                metaTree.push(tmpMetaTree);
                            });
                            treeNode.props.dataRef.children = [].concat(
                                metaTree
                            );
                            this.props.updateMetaTreeData([
                                ...this.props.metaTree
                            ]);
                            resolve();
                        }
                    }
                }
            });
        });
    };
    getContentDom = () => {
        const { metaTree } = this.props;
        return (
            <div className="template-setting-left-sider template-setting-sider">
                <div className="sider-content">
                    <div className="sider-search">
                        {/* <Input
							placeholder=''
							style={{ width: '70%' }}
							onPressEnter={this.onInputSearch}
							onChange={this.onInputChange}
							addonAfter={
								<Icon type='search' className='search-input-icon' onClick={this.onInputSearch} />
							}
						/> */}
                    </div>

                    <div className="sider-tree">
                        <Tree
                            checkable
                            checkStrictly={true}
                            loadData={this.onLoadData}
                            showLine={true}
                            multiple={true}
                            onSelect={this.onSelect}
                            selectedKeys={this.state.selectedKeys}
                            checkedKeys={this.state.checkedKeys}
                            onCheck={this.onCheck}
                        >
                            {this.renderTreeNodes(metaTree)}
                        </Tree>
                    </div>
                </div>
            </div>
        );
    };
    ///205
    selectAllTreeNode = (isChecked, metaTreeNodeList) => {
        let { selectedKeys, checkedKeys } = this.state;
        selectedKeys = _.cloneDeep(selectedKeys);
        if (isChecked) {
            //205
            //205
            _.forEach(metaTreeNodeList, (m, i) => {
                if (
                    selectedKeys.indexOf(m.myUniqID) === -1 &&
                    m.datatype !== "205"
                ) {
                    selectedKeys.push(m.myUniqID);
                    this.state.selectedNodes.push({
                        props: {
                            dataRef: {
                                ...m
                            }
                        }
                    });
                }
            });
        } else {
            _.forEach(metaTreeNodeList, (m, i) => {
                _.remove(selectedKeys, n => {
                    return n === m.myUniqID;
                });
            });
            _.forEach(metaTreeNodeList, (m, i) => {
                _.remove(this.state.selectedNodes, n => {
                    return n.props.dataRef.myUniqID === m.myUniqID;
                });
            });
        }
        this.setState({
            selectedKeys,
            checkedKeys: selectedKeys
        });
    };
    //205
    handleChangeSelectTreeNode = value => {
        const { metaTree, canSelectTreeNodeList } = this.props;
        //204
        _.forEach(canSelectTreeNodeList, m => {
            if (value.indexOf(`${m.refcode} ${m.refname}`) === -1) {
                if (m.children !== null) {
                    setTimeout(() => {
                        this.selectAllTreeNode(false, m.children);
                    }, 0);
                }
            } else {
                if (m.children !== null) {
                    setTimeout(() => {
                        this.selectAllTreeNode(true, m.children);
                    }, 0);
                }
            }
        });

        this.setState({ needToBeAllSelectNodeList: value });
    };
    //
    getModalTitleDom = () => {
        let { json } = this.props;
        return (
            <div>
                <span className="custom-title">{langCheck("ZoneSetting-000124", "pages", json)}</span>
                {/* /*  */}
                <Checkbox
                    className="dnd-cancel" //click
                    style={{ marginLeft: "25px" }}
                    onChange={e => {
                        this.selectAllTreeNode(
                            e.target.checked,
                            this.props.metaTree
                        );
                    }}
                >
                    {langCheck("ZoneSetting-000125", "pages", json)}
                    {/* /*  */}
                </Checkbox>
                {(() => {
                    if (this.props.canSelectTreeNodeList.length > 0) {
                        return (
                            <Select
                                className="dnd-cancel" //click
                                maxTagCount={1}
                                size="small"
                                style={{ width: "50%" }}
                                mode="multiple"
                                placeholder={langCheck(
                                    "ZoneSetting-000126",
                                    "pages",
                                    json
                                )} /*  */
                                onChange={this.handleChangeSelectTreeNode}
                                value={this.state.needToBeAllSelectNodeList}
                            >
                                {this.props.canSelectTreeNodeList.map(
                                    (node, index) => {
                                        return (
                                            <Option
                                                key={`${node.refcode} ${
                                                    node.refname
                                                }`}
                                                disabled={
                                                    node.children ? false : true
                                                }
                                            >
                                                {node.refcode} {node.refname}
                                            </Option>
                                        );
                                    }
                                )}
                            </Select>
                        );
                    }
                })()}
            </div>
        );
    };
    render() {
        let { json } = this.props;
        return (
            <HotKeysModal
                maskClosable={false}
                customHeader={this.getModalTitleDom()}
                // mask={false}
                wrapClassName="meta-add-modal"
                visible={this.props.modalVisible}
                onOk={this.onOkMoveDialog}
                onCancel={this.showModalHidden}
                destroyOnClose={true}
                width={"50%"}
                // style={{ top: 20 }}
                footer={[
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000005",
                            "pages",
                            json
                        )}(Alt + Y)`}
                    >
                        <Button
                            key="submit"
                            type="primary"
                            onClick={this.onOkMoveDialog}
                        >
                            {langCheck("ZoneSetting-000005", "pages", json)}(
                            <span className="underline">Y</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>,
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000006",
                            "pages",
                            json
                        )}(Alt + N)`}
                    >
                        <Button key="back" onClick={this.showModalHidden}>
                            {langCheck("ZoneSetting-000006", "pages", json)}(
                            <span className="underline">N</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>
                ]}
            >
                {this.getContentDom()}
            </HotKeysModal>
        );
    }
}
export default connect(
    state => ({}),
    { updateSelectCard, updateSelectArea }
)(TreeModal);
