import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Row, Form, Select } from "antd";
import { setZoneDataFun, updateAreaList } from "Store/ZoneSetting/action";
import { createForm } from "./CreatForm";
import { langCheck } from "Pub/js/utils";
import { getMulti } from "Pub/js/getMulti";
const Option = Select.Option;

class SetTemplateLeft extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {};
    }
    /**
     *  
     */
    getFromData = () => {
        const { getFieldsValue, validateFields } = this.props.form;
        let flag = false;
        validateFields((err, values) => {
            if (!err) {
                flag = true;
            }
        });
        return flag ? getFieldsValue() : null;
    };
    componentDidMount() {
        this.props.setZoneDataFun(this.getFromData);
    }
    /**
     * @param {*} DOMDATA
     * ,Form
     */
    getFormItem = DOMDATA => {
        let { areaList } = this.props,
            form = [],
            table = [];
        areaList &&
            areaList.map((v, i) => {
                if (v.areatype === "1") {
                    //
                    form.push(v);
                } else if (v.areatype === "2") {
                    //
                    table.push(v);
                }
            });
        //
        if (this.props.isImplement) {
            DOMDATA = DOMDATA.filter((v, i) => v.code === "name");
        }else{
            //,Form
            if (form.length !== 0 || table.length !== 0) {
                let item = DOMDATA.find(item => item.code === "headcode");
                if (item) {
                    item.options = creatOption();
                }
            } else {
                DOMDATA =
                    DOMDATA && DOMDATA.filter((v, i) => v.code !== "headcode");
            }
        }
        function creatOption() {
            let newArray =
                areaList &&
                areaList.filter((v, i) => {
                    return v.areatype !== "0";
                });
            return (
                newArray &&
                newArray.map((item, i) => {
                    return (
                        <Option key={item.code} value={item.code}>
                            {item.name}
                        </Option>
                    );
                })
            );
        }
        return DOMDATA;
    };
    render() {
        let { json } = this.props;
        let DOMDATA = [
            {
                lable: langCheck(
                    "102202APP-000005",
                    "pages",
                    json
                ) /*  */,
                type: "input",
                code: "code",
                required: true
            },
            {
                lable: langCheck(
                    "102202APP-000006",
                    "pages",
                    json
                ) /*  */,
                type: "input",
                code: "name",
                required: true,
                disabled: this.props.isImplement
            },
            {
                lable: langCheck(
                    "102202APP-000007",
                    "pages",
                    json
                ) /*  */,
                type: "input",
                code: "resid"
            },
            {
                lable: langCheck(
                    "102202APP-000008",
                    "pages",
                    json
                ) /*  */,
                type: "input",
                code: "templetdesc",
                required: false
            },
            {
                lable: langCheck(
                    "102202APP-000009",
                    "pages",
                    json
                ) /*  */,
                type: "input",
                code: "clazz",
                required: false
            },
            {
                lable: langCheck(
                    "102202APP-000010",
                    "pages",
                    json
                ) /*  */,
                type: "select",
                code: "headcode",
                options: [],
                required: true
            },
            {
                lable: langCheck(
                    "102202APP-000068",
                    "pages",
                    json
                ) /*  */,
                type: "radio",
                code: "isdefault"
            }
        ];
        DOMDATA = this.getFormItem(DOMDATA);
        return (
            <Form className="from-card">
                <Row gutter={24}>{createForm(DOMDATA, this.props)}</Row>
            </Form>
        );
    }
}
SetTemplateLeft = Form.create()(SetTemplateLeft); // form
SetTemplateLeft.PropTypes = {
    zoneArr: PropTypes.object.isRequired,
    zoneDatas: PropTypes.object.isRequired,
    setZoneDataFun: PropTypes.func.isRequired
};
export default connect(
    state => {
        let { zoneDatas, json } = state.zoneRegisterData;
        let { areaList, zoneFormData } = state.zoneSettingData;
        return { zoneDatas, json, areaList, zoneFormData };
    },
    {
        setZoneDataFun,
        updateAreaList
    }
)(SetTemplateLeft);
