import React, { Component } from 'react';
import { Button, Popconfirm, Modal } from 'antd';
import { connect } from 'react-redux';
import Ajax from 'Pub/js/ajax';
import Notice from 'Components/Notice';
import Appicon from 'Components/Appicon';
import { withRouter } from 'react-router-dom';
import PreviewModal from 'Components/TempletPreview';
import { GetQuery, langCheck } from 'Pub/js/utils';
import { openPage } from 'Pub/js/superJump';
import { ControlTip } from 'Components/ControlTip';
import { setZoneData } from 'Store/Zone/action';
import { updateAreaList, updateSaveFlag, setZoneDataFun, updateAreaValidate } from 'Store/ZoneSetting/action';
import { queryAllArea, queryTemplateProperty, findRepeatItemsByCode } from './events';
import _ from 'lodash';
const confirm = Modal.confirm;
//header
class MyHeader extends Component {
    constructor(props) {
        super(props);
        this.urlRequestObj = GetQuery(this.props.location.search);
        this.state = { 
            previewVisibel: false,
            backModalShow: false
         };
    }
    //
    setModalVisibel = (visibel) => {
        this.setState({ previewVisibel: visibel });
    };
    //
    trim = (str) => {
        if (str) {
            return str.replace(/^\s+|\s+$/g, '');
        }
    };

    //
    validateEmptyArea = () => {
        const { json, areaList ,newAreaList } = this.props;
        let newList = _.cloneDeep(areaList);
        let result = true;
        const areaCodeLang = langCheck('102202APP-000038', "pages", json);/*  */
        const areaNameLang = langCheck('102202APP-000039', "pages", json);/*  */
        let emptyMsg = [];
        let emptyArea = areaList.filter(area => !area.code || !area.name);
        const simpleArea = newAreaList.filter(r => r !== null);
        if (emptyArea && emptyArea.length > 0) {
            simpleArea.forEach((area, index) => {
                if (!area.code || !area.name){
                    const prefix = langCheck('ZoneSetting-000046', "pages", json);/*  */
                    const msg = getEmptyMsg(area, `${prefix}${index + 1}`);
                    emptyMsg.push(msg);
                }
                if (simpleArea.length !== newAreaList.length) {
                    validateEmptySpecialArea(area);
                }
            });
            result = false;
        }

        if(!result){
            Notice({
                status: 'error',
                TextArr: [
                    langCheck('ZoneSetting-000012', "pages", json), /*  */
                    langCheck('ZoneSetting-000011', "pages", json), /*  */
                    langCheck('ZoneSetting-000224', "pages", json)  /*  */
                ], 
                content: langCheck('102202APP-000012', "pages", json),/*  */
                groupOperation: true,
                groupOperationMsg: emptyMsg,
            });
            this.setAreaListValidate(newList, emptyArea);
            this.props.updateAreaList(newList);
            this.props.updateAreaValidate(result);
        }
        return result;

        ///
        function validateEmptySpecialArea(area){
            const msgObj = {
                groups: langCheck('ZoneSetting-000208', "pages", json), /*  */
                sides: {
                    edit: langCheck('ZoneSetting-000222', "pages", json), /*  */
                    browse: langCheck('ZoneSetting-000215', "pages", json) /*  */
                },
                grandsons: langCheck('ZoneSetting-000223', "pages", json) /*  */
            }
            for (const [key, stype] of Object.entries(msgObj)) {
                const areaIndex = simpleArea.findIndex(item => item.pk_area === area.pk_area) + 1;//
                const areaStr = langCheck('ZoneSetting-000046', "pages", json);/*  */
                if (key in area){
                    area[key].forEach((item, index, sArr) => {
                        if (!item.code || !item.name){
                            let suffix = key === 'sides' ? `${stype[item.areastatus]}` : `${stype}${index + 1}`;
                            const msgPrefix = `${areaStr}${areaIndex}-${suffix}`;
                            const msg = getEmptyMsg(item, msgPrefix, areaIndex);
                            emptyMsg.push(msg);
                            result = false;
                        }
                    });
                }
            }
        }

        function getEmptyMsg(area, prefix){
            let msg = '';
            if (!area.code && !area.name) { //
                msg = `${prefix}${[areaCodeLang, areaNameLang].join()}`;
            } else if (!area.code) { //
                msg = `${prefix}${areaCodeLang}`;
            } else if (!area.name) { //
                msg = `${prefix}${areaNameLang}`;
            }
            return msg;
        }
    }

    // tableCode
    validateCode = (list) => {
        const { json, } = this.props;
        let newList = _.cloneDeep(list);
        let result = true;
        let repeatArea = findRepeatItemsByCode(list);
        let isAreaEmpty = this.validateEmptyArea();
        //
        if (isAreaEmpty === false){
            return isAreaEmpty;
        }
        // code
        if (repeatArea && repeatArea.length > 0) {
            let repeatNames = repeatArea && repeatArea.map(item => item.name);
            Notice({ status: 'error', msg: `${langCheck('ZoneSetting-000046', "pages", json)}${JSON.stringify(repeatNames)}${langCheck('102202APP-000011', "pages", json)}` }); /*  */
            result = false;
            this.setAreaListValidate(newList, repeatArea);
            this.props.updateAreaList(newList);
            this.props.updateAreaValidate(result);
        }

        return result;
    };
    //
    setAreaListValidate = (newList, areas) => {
        newList.forEach((area, index) => {
            area.validate = true;
            areas.forEach(item => {
                if (area.pk_area === item.pk_area) {
                    newList[index].validate = false;
                }
            });
        });
    }
    // 
    saveTemplate = (refresh = true) => {
        return new Promise(resolve => {
            this.saveZoneData(refresh).then((newAreaData) => {
                this.saveData({
                    newAreaData: newAreaData && newAreaData.area, //
                    refresh
                }).then(resolve);
            });
        });
    }

    //
    saveZoneData = (refresh) => {
        return new Promise((resolve, reject) => {
            let { json , areaList, zoneDatas } = this.props;
            let param = GetQuery(this.props.location.search);
            let url, datas;
            url = '/nccloud/platform/templet/settempletarea.do';
            let fromData = this.props.zoneFormData;
            //
            if (areaList.some(item => item.areatype !== '0') && !fromData.headcode){
                Notice({ status: 'warning', msg: langCheck('102202APP-000073', "pages", json) }); /*  */
                return;
            }
            //  
            if (!fromData || !fromData.code || !this.trim(fromData.code) || !this.trim(fromData.name)) {
                Notice({ status: 'warning', msg: langCheck('102202APP-000063', "pages", json) }); /*  */
                return;
            }
            if (!fromData || !areaList || areaList.length <= 0) {
                Notice({ status: 'warning', msg: langCheck('102202APP-000019', "pages", json) }); /*  */
                return;
            }
            //   
            let validateResult = this.validateCode(areaList);
            let newList = _.cloneDeep(areaList);
            newList.map((item, index) => {
                if(item.newArea) {
                    item.pk_area && delete item.pk_area;
                }
                item.formPropertyList = [];
                item.queryPropertyList = [];
                item.position = (index + 1) + '';//
            })
            datas = {
                pk_page_templet: zoneDatas.pk_page_templet,
                pagecode: zoneDatas.pagecode || param.pcode,
                pageid: zoneDatas.pageid || param.pid,
                parentid: zoneDatas.parentid || 'root',
                areaList: newList,
                isdefault: zoneDatas.isdefault,
                appcode: param.appcode || zoneDatas.appcode,
                clazz: zoneDatas.clazz,
                mateid: zoneDatas.mateid
            };
            datas = Object.assign(fromData, datas);
            if (validateResult) {
                Ajax({
                    url: url,
                    data: datas,
                    info: {
                        name: langCheck('102202APP-000013', "pages", json) /*  */,
                        action: langCheck('102202APP-000014', "pages", json) /*  */
                    },
                    success: ({ data }) => {
                        if (data.success && data.data) {
                            if (!data.data.templetid) {
                                Notice({ status: 'error', msg: data.data.msg });
                            }
                            // this.props.setZoneData({});
                            if (refresh) queryAllArea.call(this, data.data.templetid);
                            resolve(data.data);
                        }
                    },
                    error: error => {
                        reject();
                        console.log(error.data);
                    }
                });
            }
        });
    };
    // newAreaData refresh
    saveData = ({ newAreaData, refresh } = {}) => {
        return new Promise((resolve) => {
            let configRefer = true;
            const { areaList, templetid, pcode, appcode, json, zoneDatas } = this.props;
            //,
            _.forEach(areaList, (al, idx) => {
                if (al.areatype === '0') {
                    //
                    _.forEach(al.queryPropertyList, (daty, kk) => {
                        if (
                            daty.datatype === '204' &&
                            (daty.refname === '-99' ||
                                daty.refname === '' ||
                                daty.refname === null ||
                                daty.refname === undefined)
                        ) {
                            Notice({
                                status: 'typeError',
                                msg: `${langCheck('ZoneSetting-000046', 'pages', json)} "${al.name}" ${langCheck('ZoneSetting-000047', 'pages', json)} "${daty.label}" ${langCheck('ZoneSetting-000048', 'pages', json)} "${langCheck('ZoneSetting-000030', 'pages', json)}" ${langCheck('ZoneSetting-000049', 'pages', json)}${langCheck('ZoneSetting-000050', 'pages', json)}`/*  ,,,,,*/
                            });
                            configRefer = false;
                        }
                    });
                }
                //
                if (al.areatype == '1') {
                    _.forEach(al.queryPropertyList, (colvalue, key) => {
                        if (colvalue.colnum < 0) {//
                            Notice({
                                status: 'typeError',
                                msg: `${langCheck('ZoneSetting-000046', 'pages', json)} "${al.name}" ${langCheck('ZoneSetting-000047', 'pages', json)} "${colvalue.label}" ${langCheck('ZoneSetting-000048', 'pages', json)} "${langCheck('ZoneSetting-000031', 'pages', json)}" ${langCheck('ZoneSetting-000051', 'pages', json)}${langCheck('ZoneSetting-000050', 'pages', json)}`/*  ,,,,,*/
                            });
                            configRefer = false;
                        }
                    });
                }
                //,
                _.forEach(al.queryPropertyList, (val, kk) => {
                    if (val.maxlength < 0) {
                        Notice({
                            status: 'typeError',
                            msg: `${langCheck('ZoneSetting-000046', 'pages', json)} "${al.name}" ${langCheck('ZoneSetting-000047', 'pages', json)} "${val.label}" ${langCheck('ZoneSetting-000048', 'pages', json)} "${langCheck('ZoneSetting-000032', 'pages', json)}" ${langCheck('ZoneSetting-000051', 'pages', json)}${langCheck('ZoneSetting-000050', 'pages', json)}`/*  ,,,,,*/
                        });
                        configRefer = false;
                    }
                });
            });
            if (!configRefer) {
                return false;
            }
            let formPropertyList = [];
            let queryPropertyList = [];
            //pk_query_property
            _.forEach(areaList, (a, index) => {
                _.forEach(a.queryPropertyList, (q) => {
                    if (q.pk_query_property.indexOf('new') !== -1) {
                        q.pk_query_property = '';
                    }
                });
            });
            //queryPropertyListformquery
            _.forEach(areaList, (a, index) => {
                if (a.newArea) {
                    const newAreaId = newAreaData && newAreaData.find(item => item.code === a.code);
                    //id
                    a.queryPropertyList.map(item => {
                        item.areaid = newAreaId && newAreaId.pk_area;
                    });
                }
                if (a.areatype === '0') {
                    queryPropertyList = queryPropertyList.concat(a.queryPropertyList);
                } else {
                    formPropertyList = formPropertyList.concat(a.queryPropertyList);
                }
            });
    
            const saveData = {};
            saveData.templetid = templetid;
            saveData.formPropertyList = formPropertyList;
            saveData.queryPropertyList = queryPropertyList;
            saveData.ismobile = this.urlRequestObj.templettype === 'mobile'; // 
            //
            Ajax({
                url: `/nccloud/platform/templet/setareaproperty.do`,
                info: {
                    name: langCheck('ZoneSetting-000017', 'pages', json),/*  */
                    action: langCheck('ZoneSetting-000033', 'pages', json)/*  */
                },
                data: saveData,
                loading: true,//loading
                success: (res) => {
                    // let param = GetQuery(this.props.location.search);
                    const { data, success } = res.data;
                    if (success) {
                        Notice({ status: 'success', msg: data });
                        //localstorageappcode_pcode
                        console.log('appcode=====', appcode);
                        console.log('pcode=====', pcode);
                        console.log('=====', `appTempletStorage_${appcode}_${pcode}`);/*  =====*/
                        localStorage.removeItem(`appTempletStorage_${appcode}_${pcode}`);
                        //
                        if (refresh && this.urlRequestObj.templetid) {
                            queryTemplateProperty.call(this);
                        }
                        this.props.updateSaveFlag(true); //
                        resolve();
                        //1909
                        // if (this.props.status) {
                        //     // 
                        //     if (this.props.status === 'templateSetting') {
                        //         openPage(`/TemplateSetting`, false, {}, [ 'status', 'templetid' ]);
                        //     } else if (this.props.status === 'templateSetting-unit') {
                        //         openPage(`/TemplateSetting-unit`, false, {}, [ 'status', 'templetid' ]);
                        //     }
                        // } else {
                        //     // 
                        //     openPage(`/ZoneSettingComplete`, false, {
                        //         templetid: this.props.templetid,
                        //         pcode: param.pcode,
                        //         pid: param.pid,
                        //         appcode: param.appcode
                        //     });
                        // }
                    } else {
                        Notice({ status: 'error', msg: data });
                    }
                }
            });
        });
    };

    toggleBackModal = flag => this.setState({ backModalShow: flag });

    //
    handleBack = () => {
        //
        if (this.props.isSave) {
            this.handleNotSaveBack();
        }else{
            this.toggleBackModal(true);
        }
        // const { json } = this.props;
        // ControlTip({
        //     status: 'warning',
        //     title: langCheck('ZoneSetting-000204', 'pages', json),/*  */
        //     msg: langCheck('ZoneSetting-000205', 'pages', json),/*  */
        //     onOk: () => {
        //         if (this.props.status) {
        //             // 
        //             if (this.props.status === 'templateSetting') {
        //                 openPage(`/TemplateSetting`, false, {}, ['status', 'templetid']);
        //             } else if (this.props.status === 'templateSetting-unit') {
        //                 openPage(`/TemplateSetting-unit`, false, {}, ['status', 'templetid']);
        //             }
        //         } else {
        //             //
        //             openPage(
        //                 `/ar`,
        //                 false,
        //                 {
        //                     b1: langCheck('ZoneSetting-000041', 'pages', json),/*  */
        //                     b2: langCheck('ZoneSetting-000042', 'pages', json),/*  */
        //                     b3: langCheck('ZoneSetting-000043', 'pages', json),/*  */
        //                     n: langCheck('ZoneSetting-000044', 'pages', json),/*  */
        //                     c: '102202APP'
        //                 },
        //                 ['templetid', 'appcode', 'pcode', 'pid']
        //             );
        //         }
        //     }
        // });
    }

    //
    back = () => {
        const { json, status } = this.props;
        if (status) {
            // 
            if (status === 'templateSetting') {
                openPage(`/TemplateSetting`, false, {}, ['status', 'templetid']);
            } else if (status === 'templateSetting-unit') {
                openPage(`/TemplateSetting-unit`, false, {}, ['status', 'templetid']);
            }
        } else {
            let jumpUrl = '/ar';
            let c = '102202APP';
            let n = langCheck('ZoneSetting-000044', 'pages', json); /*  */
            if (this.urlRequestObj.templettype === 'mobile'){
                jumpUrl = '/MobileAppRegister';
                c = '102202MOBILE';
                n = langCheck('ZoneSetting-000228', 'pages', json); /*  */
            }
            
            //
            openPage(
                jumpUrl,
                false,
                {
                    b1: langCheck('ZoneSetting-000041', 'pages', json),/*  */
                    b2: langCheck('ZoneSetting-000042', 'pages', json),/*  */
                    b3: langCheck('ZoneSetting-000043', 'pages', json),/*  */
                    n,
                    c
                },
                ['templetid', 'appcode', 'pcode', 'pid', 'templettype']
            );
        }
    }

    //
    handleSaveBack = () => {
        this.saveTemplate(false).then(this.back);
        this.toggleBackModal(false);
    }

    //
    handleNotSaveBack = () => {
        this.back();
    }

    //
    handleCancelBack = () => {
        this.toggleBackModal(false);
    }

    //
    getPreviewData = () => {
        let areaList = _.cloneDeep(this.props.areaList);
        areaList.forEach(item => {
            item.formPropertyList = [...item.queryPropertyList];
            //position
            item.formPropertyList.forEach(j => {
                j.position = j.position && j.position + '';
            });
            item.queryPropertyList.forEach(j => {
                j.position = j.position && j.position + '';
            });
        });
        return areaList;
    }
    render() {
        let { previewVisibel } = this.state;
        let { json, zoneDatas } = this.props;
        const { c } = GetQuery(window.location.hash);
        const templetData = {
            pagecode: zoneDatas.pagecode,
            appcode: zoneDatas.appcode,
            templetid: this.props.templetid
        }

        return (
            <div className='template-setting-header'>
                <div className='header-name'>
                    <i 
                        className="iconfont icon-danjufanhui" 
                        onClick={this.handleBack} 
                    /> 
                    <Appicon appCode={c} />
                    <span>{langCheck('ZoneSetting-000035', 'pages', json)}</span>
                    {/* /*  */ }
                </div>
                <div className='button-list'>
                {/* 1909 */}
                    {
                        // (() => {
                        // 
                        // if (!this.props.status) {
                        //     return (
                        //         <Button
                        //             onClick={() => {
                        //                 //
                        //                 ControlTip({
                        //                     status: 'warning',
                        //                     title: langCheck('ZoneSetting-000036', 'pages', json),/*  */
                        //                     msg: langCheck('ZoneSetting-000037', 'pages', json),/*  */
                        //                     onOk: () => {
                        //                         let param = GetQuery(this.props.location.search);
                        //                         openPage(
                        //                             `/Zone`,
                        //                             false,
                        //                             {
                        //                                 templetid: this.props.templetid,
                        //                                 pcode: param.pcode,
                        //                                 pid: param.pid,
                        //                                 appcode: param.appcode
                        //                             },
                        //                             [ 'appcode', 'pcode', 'pid' ]
                        //                         );
                        //                     }
                        //                 });
                        //             }}
                        //         >
                        //             {langCheck('ZoneSetting-000036', 'pages', json)}
                        //             {/* /*  */}
                        //         </Button>
                        //     );
                        // }
                    // })()
                    }

                    <Button onClick={() => this.saveTemplate(true)} className='ant-btn-primary'>
                        {langCheck('ZoneSetting-000038', 'pages', json)}
                        {/* /*  */}
                    </Button>
                    <Button
                        onClick={() => {
                            this.setModalVisibel(true);
                        }}
                    >
                        {langCheck('ZoneSetting-000039', 'pages', json)}
                        {/* /*  */}
                    </Button>
                    {/* <Button> */}
                        {/* {langCheck('ZoneSetting-000006', 'pages', json)} */}
                        {/* /*  */}
                    {/* </Button> */}
                </div>
                {previewVisibel && (
                    <PreviewModal 
                        previewVisibel={previewVisibel} 
                        setModalVisibel={this.setModalVisibel} 
                        templetData={templetData}
                        requestUrl="/nccloud/platform/templet/previewtemplet.do"
                        requestData={this.getPreviewData()}
                    />
                )}
                <Modal
                    mask={true}
                    closable={false}
                    title={langCheck('ZoneSetting-000207', "pages", json)}/*  */
                    visible={this.state.backModalShow}
                    centered={true}
                    maskClosable={false}
                    width={410}
                    height={200}
                    className={'prompt-box-project'}
                    footer={[
                        <Button onClick={this.handleSaveBack} type="primary">
                            {langCheck('ZoneSetting-000038', "pages", json)} {/*  */}
                        </Button>,
                        <Button onClick={this.handleNotSaveBack}>
                            {langCheck('ZoneSetting-000206', "pages", json)} {/*  */}
                        </Button>,
                        <Button onClick={this.handleCancelBack}>
                            {langCheck('ZoneSetting-000006', "pages", json)} {/*  */}
                        </Button>
                    ]}
                >        
                    <p className='prompt-title'>
                        <i className="icon iconfont icon-zhuyi1 warning" />
                        <span>{langCheck('ZoneSetting-000204', "pages", json)}</span> {/* */}
                    </p>
                    <p className='prompt-content'>{langCheck('ZoneSetting-000205', "pages", json)}</p> {/* */}
                </Modal>
            </div>
        );
    }
}
export default connect(
    (state) => {
        let { areaList, newAreaList, zoneFormData, isSave } = state.zoneSettingData;
        let { zoneDatas }  = state.zoneRegisterData;
        return { areaList, zoneFormData, zoneDatas, isSave, newAreaList };
    },
    {
        setZoneData,
        updateAreaList,
        updateSaveFlag,
        setZoneDataFun,
        updateAreaValidate
    }
)(withRouter(MyHeader));
