import React, { Component } from "react";
import { connect } from "react-redux";
import "./index.less";
import MyHeader from "./header";
import MyContent from "./content";
import MyRightSider from "./rightSider";
import MyLeftSider from "./leftSider";
import { Steps } from "antd";
import { GetQuery, langCheck } from "Pub/js/utils";
import { getMulti } from 'Pub/js/getMulti';
import { queryAllArea, queryTemplateProperty } from './events';
import { setZoneDataFun, updateAreaList } from "Store/ZoneSetting/action";
import { setZoneData, setZoneTempletid, setJson } from 'Store/Zone/action';
import Ajax from 'Pub/js/ajax';
const Step = Steps.Step;
//
class ZoneSetting extends Component {
    constructor(props) {
        super(props);
        this.urlRequestObj = GetQuery(this.props.location.search);
        this.state = {
            templetid: this.urlRequestObj.templetid,
            status: this.urlRequestObj.status, // status
            pcode: this.urlRequestObj.pcode,
            appcode: this.urlRequestObj.appcode,
            json: {},
            isLang:false,
            leftSiderShow: true, //
            isImplement: this.urlRequestObj.status === 'templateSetting' || this.urlRequestObj.status === 'templateSetting-unit' //
        };
    }
    componentWillMount(){
        let callback = (json) => {
            // console.log('json', json);
            window.multiZoneSettingLang = json;
            this.props.setJson(json);
            this.setState({
                json:json,
                isLang:true
            });
        };
        getMulti({
            moduleId: ['ZoneSetting','102202APP'],
            // currentLocale: 'zh-CN',
            domainName: 'workbench',
            callback
        });
    }
    //
	componentDidMount() {
        let param = GetQuery(this.props.location.search);
        this.props.setZoneTempletid(param.templetid);
        //
        queryAllArea.call(this).then(() => {
            queryTemplateProperty.call(this);
        });
    }
    
    //
    toggleLeftSider = () => {
        const { leftSiderShow } = this.state;
        this.setState({
            leftSiderShow: !leftSiderShow
        });
    }
    
    render() {
        let { json } = this.state;
        console.log('pcode', this.state.pcode);/*  pcode,*/
        console.log('appcode', this.state.appcode);/*  appcode,*/
		//statussteps
        // let show = this.urlRequestObj.status
        //     ? "display_none"
        //     : "template-setting-steps";
        return (
            <div className="template-setting-page">
                {/* <div className={show}>
                    <Steps size="small" current={1}>
                        <Step title={langCheck('ZoneSetting-000052', 'pages', json)} description={langCheck('ZoneSetting-000053', 'pages', json)} />
                        <Step title={langCheck('ZoneSetting-000035', 'pages', json)} description={langCheck('ZoneSetting-000054', 'pages', json)} />
                        <Step title={langCheck('ZoneSetting-000055', 'pages', json)} description="" />
                    </Steps>
                </div> */}
                <MyHeader
                    templetid={this.state.templetid}
                    status={this.state.status}
                    pcode={this.state.pcode}
                    appcode={this.state.appcode}
                    json={this.state.json}
                    isImplement={this.state.isImplement}
                />
                <div className="template-setting-container">
                    <MyLeftSider 
                        json={this.state.json} 
                        status={this.state.status}
                        show={this.state.leftSiderShow}
                        onToggleBtnClick={this.toggleLeftSider}
                    />
                    <MyContent
                        templetid={this.state.templetid}
                        status={this.state.status}
                        json={this.state.json}
                        isImplement={this.state.isImplement}
                    />
                    {
                        this.state.isLang ? 
                            <MyRightSider 
                                json={this.state.json} 
                                status={this.state.status}
                                isImplement={this.state.isImplement}
                            />
                        : null
                    }
                </div>
            </div>
        );
    }
}
export default connect(
    state => ({
        
    }),
    { setJson, setZoneData, setZoneTempletid, setZoneDataFun, updateAreaList}
)(ZoneSetting);
