import React, { Component } from "react";
import { connect } from "react-redux";
import {
    updateSelectCard,
    updateAreaList,
    updateSelectArea,
    clearData
} from "Store/ZoneSetting/action";
import { clearZoneData } from 'Store/Zone/action';
import Ajax from "Pub/js/ajax";
import "./index.less";
import withDragDropContext from "Pub/js/withDragDropContext";
import AreaItem from "./areaItem";
import TreeModal from "./treeModal";
import _ from "lodash";
import Notice from "Components/Notice";
import { DropTarget } from 'react-dnd';
import { langCheck } from 'Pub/js/utils';
import { addNewAreaItem, getFormHeadCode } from './events';
import emptycontentImg from "Assets/images/template-empty-content.png";
//

const areaTarget = {
    // hover(props, monitor, component) {
    //     const dragItem = monitor.getItem();
        
    // },
    drop(props, monitor, component) {
        const dragItem = monitor.getItem();
        if(!dragItem.type) return;
        //
        if (dragItem.type === 'area' && dragItem.areaType !== 'grandson'){
            addNewAreaItem(props, { 
                items: { areatype: dragItem.areaCode },
                isExpand: true
            });
        }
    },
    canDrop(props, monitor) {
        const dropItem = monitor.getItem();
        return dropItem.type !== 'card';
    }
};

//DropTarget
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
    canDrop: monitor.canDrop()
}))
class MyContent extends Component {
    constructor(props) {
        super(props);

        this.state = {
            modalVisible: false,
            metaTree: [],
            targetAreaID: "",
            targetAreaType: "",
            canSelectTreeNodeList: []
        };
    }
    componentWillUnmount() {
        this.props.clearData();
        this.props.clearZoneData();
    }
    componentDidMount() {
    }
    //
    setModalVisible = modalVisible => {
        this.setState({ modalVisible });
    };
    /**
     * 
     * @param {String} metaid
     * @param {String} targetAreaID ID
     * @param {String} areatype 
     **/
    addMetaInArea = (metaid, targetAreaID, areatype) => {
        let { json } = this.props;
        if (metaid === "" || metaid === null || metaid == undefined) {
            Notice({ status: "warning", msg: langCheck('ZoneSetting-000019', 'pages', json) });/*  */
            return;
        }
        Ajax({
            url: `/nccloud/platform/templet/querymetapro.do`,
            info: {
                name: langCheck('ZoneSetting-000017', 'pages', json),/*  */
                action: langCheck('ZoneSetting-000020', 'pages', json)/*  */
            },
            data: {
                metaid: metaid
            },
            success: res => {
                if (res) {
                    let { data, success } = res.data;
                    if (success && data && data.rows && data.rows.length > 0) {
                        let metaTree = [];
                        //
                        data.rows.map((r, index) => {
                            metaTree.push({
                                ...r,
                                title: `${r.refcode} ${r.refname}`,
                                key: `${r.refcode}`,
                                myUniqID: `${r.refcode}`,
                                isLeaf: r.isleaf
                            });
                        });
                        //205select
                        let canSelectTreeNodeList = [];
                        _.forEach(metaTree, m => {
                            if (m.datatype === "205") {
                                canSelectTreeNodeList.push(m);
                            }
                        });
                        this.setState({
                            metaTree: metaTree,
                            targetAreaID: targetAreaID,
                            targetAreaType: areatype,
                            canSelectTreeNodeList: canSelectTreeNodeList
                        });
                        this.setModalVisible(true);
                    } else {
                        if (success && data && data.rows && !data.rows.length) {
                            Notice({ status: "warning", msg: langCheck('ZoneSetting-000021', 'pages', json) });/*  */
                        }
                    }
                }
            }
        });
    };
    //
    updateMetaTreeData = metaTree => {
        this.setState({ metaTree: metaTree });
    };
    //
    addCard = addCardList => {
        let { targetAreaID } = this.state;
        let { areaList } = this.props;
        areaList = _.cloneDeep(areaList);
        let targetAreaIndex = -1;
        let metaid = "";
        //areaList
        _.forEach(areaList, (a, i) => {
            if (targetAreaID === a.pk_area) {
                targetAreaIndex = i;
                metaid = a.metaid;
                return false;
            }
        });
        //code
        areaList[targetAreaIndex].queryPropertyList = _.uniqBy(
            areaList[targetAreaIndex].queryPropertyList.concat(addCardList),
            "code"
        );
        //positionclassid
        _.forEach(areaList[targetAreaIndex].queryPropertyList, (q, i) => {
            q.position = i + 1;
            q.classid = metaid;
        });
        this.props.updateAreaList(areaList);
    };
    /**
     * 
     * @param {Number} dragIndex index
     * @param {Number} hoverIndex index
     * @param {Number} areaItemIndex index
     **/
    //
    // moveCard = (dragIndex, hoverIndex, areaItemIndex) => {
    //     let { areaList } = this.props;
    //     areaList = _.cloneDeep(areaList);
    //     const cards = areaList[areaItemIndex].queryPropertyList;

    //     const dragCard = cards[dragIndex];
    //     cards.splice(dragIndex, 1);
    //     cards.splice(hoverIndex, 0, dragCard);
    //     _.forEach(cards, (q, i) => {
    //         q.position = i + 1;
    //     });
    //     this.props.updateAreaList(areaList);
    //     // this.props.updateSelectCard({}); //
    // };
    /**
     * 
     * @param {Number} cardIndex index
     * @param {Number} areaItemIndex index
     **/
    deleteCard = (cardIndex, areaItemIndex) => {
        let { areaList } = this.props;
        areaList = _.cloneDeep(areaList);

        const cards = areaList[areaItemIndex].queryPropertyList;

        cards.splice(cardIndex, 1);

        this.props.updateAreaList(areaList);
        this.props.updateSelectCard({});
    };
    /**
     * 
     * @param {Number} cardIndex index
     * @param {Number} areaItemIndex index
     **/
    //card
    // selectThisCard = (cardIndex, areaItemIndex) => {
    //     let { areaList } = this.props;
    //     let card = areaList[areaItemIndex].queryPropertyList[cardIndex];
    //     this.props.updateSelectCard(card);
    // };
    
    /**
	 * 
	 * @param {Number} dragIndex index
	 * @param {Number} hoverIndex index
	**/
    dragAreaItem = (dragIndex, hoverIndex) => {
        let { areaList } = this.props;
        areaList = _.cloneDeep(areaList);
        const dragItem = areaList[dragIndex];
        areaList.splice(dragIndex, 1);
        areaList.splice(hoverIndex, 0, dragItem);
        this.props.updateAreaList(areaList);
    };

    //
    moveUp = (areaList, areaIndex) => {
        let targetIndex = areaIndex - 1;
        if (this.props.newAreaList[targetIndex] === null) {
            //nullareaItem
            let tempArr = areaList.slice(0, targetIndex);
            targetIndex = _.findLastIndex(tempArr, item => item !== null) - 1;
        }
        return this.swapAreaItem(areaList, areaIndex, targetIndex);
    }

    //
    moveDown = (areaList, areaIndex) => {
        let targetIndex = areaIndex + 1;
        if (this.props.newAreaList[targetIndex] === null) {
            //nullareaItem
            targetIndex = areaList.slice(targetIndex).findIndex(item => item !== null) + targetIndex + 1;
        }
        return this.swapAreaItem(areaList, areaIndex, targetIndex);
    }

    //
    moveArea = (moveType, areaList, areaIndex) => {
        const currArea = areaList[areaIndex]; //
        const headCode = getFormHeadCode(areaList, this.props.zoneFormData.headcode);
        let targetIndex
        if (moveType === 'up'){ //
            targetIndex = areaIndex - 1;
            //
            if (areaList[targetIndex].relationcode === headCode) {
                let headIndex = areaList.findIndex(item => item.code === headCode); //index
                this.moveFormGroup('down', areaList, headIndex);
            } else {
                this.moveUp(areaList, areaIndex);
            }
        } else if (moveType === 'down'){ //
            targetIndex = areaIndex + 1;
            //
            if (areaList[targetIndex].code === headCode){
                this.moveFormGroup('up', areaList, targetIndex);
            } else {
                this.moveDown(areaList, areaIndex);
            }
        }
        
    }
    /**
     * /
     *  @param type  /
     *  @param areaIndex index
     *
     */
    moveAreaItem = (type, areaIndex) => {
        let areaList = _.cloneDeep(this.props.areaList);
        const currArea = areaList[areaIndex]; //
        const headCode = getFormHeadCode(areaList, this.props.zoneFormData.headcode);
        if (type === 'up' && areaIndex === 0) return; //
        if (type === 'down' && areaIndex === areaList.length - 1) return; //
        //
        if (currArea.code === headCode) {
            this.moveFormGroup(type, areaList, areaIndex);
        } else {
            this.moveArea(type, areaList, areaIndex);
        }
        this.props.updateAreaList(areaList);
    }

    /**
     * 
     * 
     *  @param moveType  up: down:
     *  @param areaList 
     *  @param areaIndex index
     */
    moveFormGroup = (moveType, areaList, areaIndex) => {
        const formGroupList = this.getFormGroupList(areaList);
        if(moveType === 'up'){
            let itemIndex = areaIndex - 1;
            this.insertAreaItem(areaList, areaIndex + formGroupList.length, itemIndex);
        } else if(moveType === 'down'){
            let itemIndex = areaIndex + formGroupList.length + 1;
            this.insertAreaItem(areaList, areaIndex, itemIndex);
        }
    }

    /**
     * 
     *
     *  @param areaList 
     *  @param targetIndex index
     *  @param sourceIndex index
     */
    swapAreaItem = (areaList, sourceIndex, targetIndex) => {
        return [areaList[sourceIndex], areaList[targetIndex]] = [areaList[targetIndex], areaList[sourceIndex]];
    }

    /**
     * 
     *
     *  @param areaList 
     *  @param targetIndex index
     *  @param itemIndex index
     */
    insertAreaItem = (areaList, targetIndex, itemIndex) => {
        let targetArea = areaList.splice(itemIndex, 1)[0];
        areaList.splice(targetIndex, 0, targetArea);
    }
    /**
     * /
     *  @param type  /
     *  @param areaIndex index
     *
     */
    moveFormGroupItem = (type, areaIndex) => {
        const { areaList } = this.props  ;
        const headCode = getFormHeadCode(areaList, this.props.zoneFormData.headcode);
        const prevGroupIndex = _.findLastIndex(areaList, item => item.relationcode === headCode && item.areatype === '1', areaIndex - 1); //
        const nextGroupIndex = _.findIndex(areaList, item => item.relationcode === headCode && item.areatype === '1', areaIndex); //
        this.moveChildrenItem(type, areaIndex, prevGroupIndex, nextGroupIndex);
    }

    /**
    * /
    *  @param type  /
    *  @param areaIndex index
    *  @param grandson 
    *
    */
    moveGrandsonItem = (type, areaIndex, grandson) => {
        const { areaList } = this.props;
        const prevGrandsonIndex = _.findLastIndex(areaList, item => item.relationcode === grandson.relationcode && item.isgrandson === true, areaIndex - 1); //
        const nextGrandsonIndex = _.findIndex(areaList, item => item.relationcode === grandson.relationcode && item.isgrandson === true, areaIndex); //
        this.moveChildrenItem(type, areaIndex, prevGrandsonIndex, nextGrandsonIndex);
    }    

    ////
    moveChildrenItem = (type, areaIndex, prevChildIndex, nextChildIndex) => {
        let areaList = _.cloneDeep(this.props.areaList);
        //
        if (type === 'up') {
            let targetindex = prevChildIndex < 0 ? 0 : prevChildIndex;
            this.insertAreaItem(areaList, targetindex, areaIndex);
        }
        //
        if (type === 'down') {
            this.insertAreaItem(areaList, nextChildIndex + 1, areaIndex);
        }
        this.props.updateAreaList(areaList);
    }
	/**
	 * 
	 * @param {String} areaID id
	**/
    deleteAreaItem = (areaID, callback) => {
        let areaList = _.cloneDeep(this.props.areaList);
        _.remove(areaList, item => item.pk_area === areaID);
        this.props.updateAreaList(areaList);
        callback && callback();
    };

    /**
     * /
     * 
     * index - index
     * flag - (true)(false)
     */
    expandArea = (index, flag) => {
        let areaList = _.cloneDeep(this.props.areaList);
        areaList[index].isShow = flag;
        this.props.updateAreaList(areaList);
    }

    //
    getFormGroupList = (areaList) => {
        let result = [...areaList];
        const relationList = result.filter(item => item.relationcode); //
        let headCode = getFormHeadCode(result, this.props.zoneFormData.headcode);
        result = relationList.filter(r => r.relationcode === headCode);
        return result;
    }

    render() {
        let { json, canDrop, connectDropTarget, areaList, newAreaList, templetid } = this.props;
        let backgroundColor = '#fff';
        //
        if (canDrop) {
            backgroundColor = 'rgba(221, 236, 250, 0.3)'
        }
        console.log({newAreaList})
        return connectDropTarget(
            <div className="template-setting-content" style={{ backgroundColor }}>
            {
                newAreaList.length > 0 ? 
                    newAreaList.map((a, i) => {
                        if(a){
                            return (
                                <AreaItem
                                    areaItem={a}
                                    key={i}
                                    areaListLength={newAreaList.length}
                                    id={a.pk_area}
                                    index={i}
                                    areatype={a.areatype}
                                    metaid={a.metaid}
                                    isShow={a.isShow}
                                    // moveCard={this.moveCard}
                                    deleteCard={this.deleteCard}
                                    addMetaInArea={this.addMetaInArea}
                                    // selectThisCard={this.selectThisCard}
                                    onDragArea={this.dragAreaItem}
                                    onMoveArea={this.moveAreaItem}
                                    onMoveFormGroup={this.moveFormGroupItem}
                                    onMoveGrandson={this.moveGrandsonItem}
                                    onDeleteArea={this.deleteAreaItem}
                                    onExpandArea={this.expandArea}
                                    json={json}
                                    pageStatus={this.props.status}
                                    isImplement={this.props.isImplement}
                                />
                            );
                        }
                    })
                    :
                    <div className="empty-content">
                        {
                            //
                            !templetid && 
                            <div>
                                <img src={emptycontentImg} />
                                {/*  */}
                                <p>{langCheck('ZoneSetting-000199', 'pages', json)}</p>
                                {/*  */}
                                <p>{langCheck('ZoneSetting-000201', 'pages', json)}</p>
                            </div>
                        }
                    </div>
            }
                
                {/*  */}
                <TreeModal
                    metaTree={this.state.metaTree}
                    canSelectTreeNodeList={this.state.canSelectTreeNodeList}
                    modalVisible={this.state.modalVisible}
                    targetAreaID={this.state.targetAreaID}
                    targetAreaType={this.state.targetAreaType}
                    targetAreaCardLength={this.state.targetAreaCardLength}
                    setModalVisible={this.setModalVisible}
                    addCard={this.addCard}
                    updateMetaTreeData={this.updateMetaTreeData}
                    json={this.props.json}
                />
            </div>
        );
    }
}
export default connect(
    state => ({
        areaList: state.zoneSettingData.areaList,
        newAreaList: state.zoneSettingData.newAreaList,
        zoneFormData: state.zoneSettingData.zoneFormData
    }),
    {
        clearData,
        clearZoneData,
        updateAreaList,
        updateSelectArea,
        updateSelectCard
    }
)(withDragDropContext(MyContent));
