import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Tabs, Input, Select, Radio } from "antd";
import { areatype, isunfold, areastatus } from "../utilService";
import { updateAreaList, updateSelectArea } from "Store/ZoneSetting/action";
import { langCheck } from "Pub/js/utils";
import { ControlTip } from "Components/ControlTip";
import MdDefaultClassEntityRef from "Components/Refers/mdDefaultClassEntityRef";
const Option = Select.Option;
const TabPane = Tabs.TabPane;
const RadioGroup = Radio.Group;
let formItemStyle = { height: 28, width: '100%' };
class SetTemplateRight extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    // 
    relationcode = () => {
        let { areaList, selectedArea } = this.props;
        return (
            areaList &&
            areaList.length &&
            areaList
                .filter(e => {
                    return e.pk_area !== selectedArea.pk_area;
                })
                .map((e, i) => {
                    return { name: e.code, value: e.code };
                })
        );
    };
    /**
     * input
     * @param {String} value value
     * @param {String} propertyKey 
     */
    onPressEnter = (value, property) => {
        this[`${property}input`].blur();
    };
    /**
     * inputreduxselectedArea/areaList
     * @param {String} value value
     * @param {String} propertyKey 
     */
    changeTemplateValue = (value, propertyKey) => {
        let { selectedArea, areaList, json } = this.props;
        areaList = _.cloneDeep(areaList);
        if (_.isEmpty(selectedArea)) {
            return;
        }
        let oldValue = _.cloneDeep(selectedArea);
        selectedArea = { ...selectedArea };
        if (propertyKey === "metaObj") { 
            let currAreaIndex = areaList.findIndex(item => item.pk_area === selectedArea.pk_area);//index
            if (!_.isEmpty(areaList[currAreaIndex].queryPropertyList)) { //
                ControlTip({
                    status: "warning",
                    title: langCheck(
                        "102202APP-000021",
                        "pages",
                        json
                    ) /*  */,
                    msg: langCheck(
                        "102202APP-000071",
                        "pages",
                        json
                    ) /*  ,*/,
                    onOk: () => {
                        setMetaData.call(this);
                    },
                    onCancel: () => {
                        selectedArea["metaname"] = oldValue.metaname;
                        selectedArea["metaid"] = "root";
                        selectedArea["refcode"] = oldValue.refcode;
                        this.props.updateSelectArea(selectedArea); //
                    }
                });
                return;
            }
            setMetaData.call(this);
        } else {
            selectedArea[propertyKey] = value;
            areaList.map(item => {
                if (item.pk_area == selectedArea.pk_area) {
                    item[propertyKey] = value;
                }
            });
            this.props.updateSelectArea(selectedArea); //
            this.props.updateAreaList(areaList); //
        }

        //
        function setMetaData(){
            selectedArea["metaname"] = value.refname;
            selectedArea["metaid"] = value.refpk;
            selectedArea["refcode"] = value.refcode;
            selectedArea["queryPropertyList"] = [];
            selectedArea["formPropertyList"] = [];
            areaList.map(item => {
                if (item.code == selectedArea.code) {
                    item["metaname"] = value.refname;
                    item["metaid"] = value.refpk;
                    item["refcode"] = value.refcode;
                    item["queryPropertyList"] = [];
                    item["formPropertyList"] = [];
                }
            });
            this.props.updateSelectArea(selectedArea); //
            this.props.updateAreaList(areaList); //
        }
    };
    /**
     * -
     * @param {String} placeholder 
     * @param {String} property 
     * @return {} 
     */
    getTemplateInput = (placeholder, property) => {
        let inputStyle = formItemStyle;
        const { areaList, selectedArea, validate } = this.props;
        const areaIndex = areaList.findIndex(item => item.pk_area === selectedArea.pk_area);
        const curArea = areaList[areaIndex];
        if (validate === false && curArea && ['code', 'name'].includes(property) && curArea[property] === ''){
            inputStyle = { ...formItemStyle, ...{ borderColor: '#f5222d' } };
        }
        return (
            <Input
                style={inputStyle}
                value={this.props.selectedArea[property]}
                onChange={e => {
                    this.changeTemplateValue(e.target.value, property);
                }}
                onBlur={e => {
                    this.changeTemplateValue(e.target.value, property);
                }}
                ref={input => (this[`${property}input`] = input)}
                onPressEnter={e => {
                    this.onPressEnter(e.target.value, property);
                }}
                disabled={this.getItemDisabled(property)}
            />
        );
    };
    /**
     * -
     * @param {String} mySelectObj option
     * @param {String} property 
     * @return {} 
     */
    getTemplateSelect = (mySelectObj, property) => {
        return (
            <Select
                showSearch
                optionFilterProp="children"
                filterOption={(input, option) =>
                    option.props.children
                        .toLowerCase()
                        .indexOf(input.toLowerCase()) >= 0
                }
                value={this.props.selectedArea[property]}
                onChange={value => {
                    this.changeTemplateValue(value, property);
                }}
                style={formItemStyle}
                disabled={this.getItemDisabled(property)}
            >
                {(() => {
                    return (
                        mySelectObj &&
                        mySelectObj.map((c, index) => {
                            return (
                                <Option key={index} value={c.value}>
                                    {c.name}
                                </Option>
                            );
                        })
                    );
                })()}
            </Select>
        );
    };
    /**
     * -
     * @param {String} placeholder 
     * @return {} 
     */
    getTemplateRefer = placeholder => {
        let { selectedArea } = this.props;
        let initialValue = {
            refcode: selectedArea ? selectedArea.refcode : "",
            refname: selectedArea ? selectedArea.metaname : "",
            refpk: selectedArea ? selectedArea.metaid : ""
        };
        return (
            <MdDefaultClassEntityRef
                value={initialValue}
                onChange={value => {
                    this.changeTemplateValue(value, "metaObj");
                }}
                disabled={this.props.isImplement} //
            />
        );
    };
    /**
     * -
     * @param {String} mySelectObj option
     * @param {String} property 
     * @return {} 
     */
    getTemplateRadio = (mySelectObj, property) => {
        return (
            <RadioGroup
                defaultValue={
                    this.props.selectedArea[property]
                        ? this.props.selectedArea[property]
                        : null
                }
                value={this.props.selectedArea[property]}
                onChange={event => {
                    this.changeTemplateValue(event.target.value, property);
                }}
                style={formItemStyle}
                disabled={this.getItemDisabled(property)}
            >
                <Radio value={true}>
                    {langCheck("102202APP-000069", "pages", this.props.json)}
                </Radio>
                <Radio value={false}>
                    {langCheck("102202APP-000070", "pages", this.props.json)}
                </Radio>
            </RadioGroup>
        );
    };

    //
    getItemDisabled = (key) => {
        const enableItems = ['name', 'isunfold'];///
        return this.props.isImplement && !enableItems.includes(key);
    }
    render() {
        let { json, selectedArea } = this.props;
        return (
            <Tabs defaultActiveKey="1">
                <TabPane
                    tab={langCheck("102202APP-000067", "pages", json)}
                    key="1"
                >
                    <ul className="basic-property rightTemplate">
                        <li className="required">
                            {langCheck("102202APP-000038", "pages", json)}
                        </li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateInput(
                                langCheck("102202APP-000038", "pages", json),
                                "code"
                            )}
                        </li>
                        <li className="required">
                            {langCheck("102202APP-000039", "pages", json)}
                        </li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateInput(
                                langCheck("102202APP-000039", "pages", json),
                                "name"
                            )}
                        </li>
                        <li>{langCheck("102202APP-000040", "pages", json)}</li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateSelect(areatype(json), "areatype")}
                        </li>
                        <li>{langCheck("102202APP-000064", "pages", json)}</li>
                        {/* /*  /*/}
                        <li>
                            {this.getTemplateSelect(isunfold(json), "isunfold")}
                        </li>
                        <li>{langCheck("102202APP-000041", "pages", json)}</li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateInput(
                                langCheck("102202APP-000041", "pages", json),
                                "areadesc"
                            )}
                        </li>
                        <li>{langCheck("102202APP-000042", "pages", json)}</li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateSelect(
                                this.relationcode(),
                                "relationcode"
                            )}
                        </li>
                        <li>{langCheck("102202APP-000043", "pages", json)}</li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateInput(
                                langCheck("102202APP-000043", "pages", json),
                                "clazz"
                            )}
                        </li>
                        <li>{langCheck("102202APP-000044", "pages", json)}</li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateSelect(
                                areastatus(json),
                                "areastatus"
                            )}
                        </li>
                        <li>{langCheck("102202APP-000036", "pages", json)}</li>
                        {/* /*  */}
                        <li>
                            {this.getTemplateRefer(
                                langCheck("102202APP-000036", "pages", json)
                            )}
                        </li>
                        {selectedArea.areatype &&
                            selectedArea.areatype == "2" && (
                                <li>
                                    {langCheck(
                                        "102202APP-000072",
                                        "pages",
                                        json
                                    )}
                                </li>
                            )}
                        {/* /*  */}
                        {selectedArea.areatype &&
                            selectedArea.areatype == "2" && (
                                <li className="isgrandsonRadio">
                                    {this.getTemplateRadio(
                                        langCheck(
                                            "102202APP-000072",
                                            "pages",
                                            json
                                        ),
                                        "isgrandson"
                                    )}
                                </li>
                            )}
                    </ul>
                </TabPane>
            </Tabs>
        );
    }
}
SetTemplateRight.PropTypes = {};
export default connect(
    state => ({
        areaList: state.zoneSettingData.areaList,
        selectedArea: state.zoneSettingData.selectedArea,
        validate: state.zoneSettingData.validate
    }),
    {
        updateAreaList,
        updateSelectArea
    }
)(SetTemplateRight);
