import React, { Component } from "react";
import _ from "lodash";
import { connect } from "react-redux";
import { Input , Button , Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import { updateAreaList, updateSelectCard, updateSelectArea } from "Store/ZoneSetting/action";
import { getItemtypeByDatatype, colorObj } from './utilService';
import { langCheck } from 'Pub/js/utils';
import { selectCardFromOther } from './events';
//
class AddNotMetaDataModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            notMetaDataName: "",
            notMetaDataCode: "",
            explainForName: "",
            explainForCode: ""
        };
    }
    //
    showModalHidden = () => {
        this.props.setAddDataModalVisibel(false);
        //
        this.setState({
            notMetaDataName: "",
            notMetaDataCode: "",
            explainForName: "",
            explainForCode: ''
        })
    };
    //
    onOkDialog = () => {
        //
        let flag = true;
        const { notMetaDataName, notMetaDataCode } = this.state;
        let { json, propperty } = this.props;
        if (
            this.checkDataCorrect(notMetaDataName).correct &&
            this.checkDataCorrect(notMetaDataCode, "code").correct
        ) {
            flag = false;
        }
        if(flag) {
            if(notMetaDataName == '') {
                this.setState({
                    explainForName: langCheck('ZoneSetting-000000', 'pages', json)/*  */
                })
            }
            if(notMetaDataCode == '') {
                this.setState({
                    explainForCode: langCheck('ZoneSetting-000000', 'pages', json)/*  */
                })
            }
            return;
        }
        //
        let { areaList, areaIndex } = this.props;
        let queryPropertyList = areaList[areaIndex].queryPropertyList;
        let cardObj = {};
        const itemtype = getItemtypeByDatatype(propperty.datatype);
        if (this.props.areatype === "0") {
            //
            cardObj = {
                pk_query_property: "newNotMetaData" + new Date().getTime(),
                areaid: areaList[areaIndex].pk_area,
                label: this.state.notMetaDataName,
                code: this.state.notMetaDataCode,
                metapath: "",
                isnotmeta: true,
                isuse: true,
                position: `${queryPropertyList.length + 1}`,
                opersign: "=@>@>=@<@<=@like@",
                opersignname: langCheck('ZoneSetting-000001', 'pages', json),/*  @@@@@@*/
                defaultvalue: "",
                isfixedcondition: false,
                required: false,
                disabled: false,
                visible: true,
                ismultiselectedenabled: false,
                isquerycondition: false,
                datatype: propperty.datatype || "1",
                refname: "-99",
                containlower: false,
                ischeck: false,
                isbeyondorg: false,
                usefunc: false,
                showtype: "1",
                returntype: "refpk",
                define1: "",
                define2: "",
                define3: "",
                define4: "",
                define5: "",
                itemtype: itemtype || "input",
                visibleposition: "",
                tablecode: "",
                querytablename: ""
            };
        } else {
            //
            cardObj = {
                pk_query_property: "newNotMetaData" + new Date().getTime(),
                areaid: areaList[areaIndex].pk_area,
                code: this.state.notMetaDataCode,
                datatype: propperty.datatype || "1",
                label: this.state.notMetaDataName,
                position: `${queryPropertyList.length + 1}`,
                metapath: "",
                color: colorObj() && colorObj()[0].value, //
                isrevise: false,
                required: false,
                disabled: false,
                visible: true,
                maxlength: "20",
                defaultvalue: "",
                hyperlinkflag: false,//
                defaultvar: "",
                define1: "",
                define2: "",
                define3: "",
                itemtype: itemtype || "input"
            };
        }
        if (this.props.areatype === "1") {
            //
            cardObj.colnum = "1";
            cardObj.isnextrow = false;
        }
        if (this.props.areatype === "2") {
            //
            cardObj.width = "";
            cardObj.istotal = false;
        }
        areaList[areaIndex].queryPropertyList = queryPropertyList.concat(
            cardObj
        );
        this.setState({
            notMetaDataName: "",
            notMetaDataCode: "",
            explainForName: "",
            explainForCode: ""
        });
        this.props.updateAreaList(areaList);
        selectCardFromOther.call(this, cardObj); //
        this.showModalHidden();
    };
    /**
     * codecode
     * @param {String} checkCode code
     * @return {Boolean} flag /
     */
    isUniqueInQueryList = checkCode => {
        let { areaList, areaIndex } = this.props;
        let queryPropertyList = areaList[areaIndex].queryPropertyList;
        let flag = true;
        _.forEach(queryPropertyList, q => {
            if (q.code === checkCode) {
                flag = false;
                return false;
            }
        });
        return flag;
    };
    /**
     * 
     * @param {String} checkedStr 
     * @param {String} type 
     * @return {Object} &
     */
    checkDataCorrect = (checkedStr, type) => {
        let correct = true;
        let errorMsg = "";
        let { json } = this.props;
        if (type === "code") {
            if (checkedStr === "") {
                errorMsg = langCheck('ZoneSetting-000000', 'pages', json);/*  */
                correct = false;
            } else {
                //
                let strRegExp = /[\u4e00-\u9fa5]/;
                if (strRegExp.test(checkedStr)) {
                    errorMsg = langCheck('ZoneSetting-000002', 'pages', json);/*  */
                    correct = false;
                }
                if (!this.isUniqueInQueryList(checkedStr)) {
                    errorMsg = langCheck('ZoneSetting-000003', 'pages', json);/*  */
                    correct = false;
                }
            }
        } else {
            if (checkedStr === "") {
                errorMsg = langCheck('ZoneSetting-000000', 'pages', json);/*  */
                correct = false;
            }
        }
        return { correct: correct, errorMsg: errorMsg };
    };
    /**
     * namecode
     * @return {Boolean} 
     */
    checkNameAndCodeCorrect = () => {
        let flag = false;
        const { notMetaDataName, notMetaDataCode } = this.state;
        if (
            this.checkDataCorrect(notMetaDataName).correct &&
            this.checkDataCorrect(notMetaDataCode, "code").correct
        ) {
            flag = true;
        }
        return flag;
    };
    //
    onPressEnter = () => {
        if (!this.checkNameAndCodeCorrect()) {
            return;
        }
        this.onOkDialog();
    };
    //
    changeNotMetaDataName = e => {
        this.setState({ notMetaDataName: e.target.value });
        const { correct, errorMsg } = this.checkDataCorrect(e.target.value);
        this.setState({ explainForName: errorMsg });
    };
    //
    changeNotMetaDataCode = e => {
        this.setState({ notMetaDataCode: e.target.value });
        const { correct, errorMsg } = this.checkDataCorrect(
            e.target.value,
            "code"
        );
        this.setState({ explainForCode: errorMsg });
    };
    //=>
    componentWillUpdate = (nextProps, nextState) => {
        if (!this.props.addDataModalVisibel && nextProps.addDataModalVisibel) {
            setTimeout(() => {
                this.refs.addNotMetaDataInputDom.focus();
            }, 0);
        }
    };
    render() {
        let { json } = this.props;
        return (
            <HotKeysModal
                maskClosable={false}
                customHeader={langCheck('ZoneSetting-000004', 'pages', json)}/*  */
                // mask={false}
                wrapClassName="add-not-meta-data"
                visible={this.props.addDataModalVisibel}
                onOk={this.onOkDialog}
                onCancel={this.showModalHidden}
                footer={[
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck('ZoneSetting-000005', 'pages', json)}(Alt + Y)`}
                    >
                        <Button
                            key="submit"
                            // disabled={!this.checkNameAndCodeCorrect()}
                            type="primary"
                            onClick={this.onOkDialog}
                        >
                            {langCheck('ZoneSetting-000005', 'pages', json)}
                            (<span className="underline">Y</span>)
                            {/* /*  */ }
                        </Button>
                    </Tooltip>,
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck('ZoneSetting-000006', 'pages', json)}(Alt + N)`}
                    >
                        <Button key="back" onClick={this.showModalHidden}>
                            {langCheck('ZoneSetting-000006', 'pages', json)}
                            (<span className="underline">N</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>
                ]}
            >
                <div>
                    <div 
                        className="code-div" 
                        ref="addNotMetaCodeDiv"
                        style={ this.state.explainForCode === ""
                            ? {marginBottom: '19.09px'}
                            : {}
                        }
                        >
                        <span>{langCheck('ZoneSetting-000007', 'pages', json)}</span>
                        {/* /*  */ }
                        <Input
                            className={
                                this.state.explainForCode === ""
                                    ? ""
                                    : "has-error"
                            }
                            ref="addNotMetaDataInputDom"
                            placeholder={langCheck('ZoneSetting-000008', 'pages', json)}/*  */
                            value={this.state.notMetaDataCode}
                            onChange={this.changeNotMetaDataCode}
                            onPressEnter={this.onPressEnter}
                        />
                        <br/>
                        <div
                            style={{
                                marginLeft: '26%',
                                visibility:
                                    this.state.explainForCode === ""
                                        ? "hidden"
                                        : "visible"
                            }}
                            className="form-explain"
                        >
                            {this.state.explainForCode}
                        </div>
                    </div>
                    <div 
                        ref="addNotMetaNameDiv" 
                        className='not-code'
                        style={ this.state.explainForName === ""
                            ? {marginBottom: '19.09px'}
                            : {}
                        }
                    >
                        <span>{langCheck('ZoneSetting-000009', 'pages', json)}</span>
                        {/* /*  */ }
                        <Input
                            className={
                                this.state.explainForName === ""
                                    ? ""
                                    : "has-error"
                            }
                            placeholder={langCheck('ZoneSetting-000010', 'pages', json)}/*  */
                            value={this.state.notMetaDataName}
                            onChange={this.changeNotMetaDataName}
                            onPressEnter={this.onPressEnter}
                        />
                        <br/>
                        <div
                            style={{
                                marginLeft: '26%',
                                visibility:
                                    this.state.explainForName === ""
                                        ? "hidden"
                                        : "visible"
                            }}
                            className="form-explain"
                        >
                            {this.state.explainForName}
                        </div>
                    </div>
                </div>
            </HotKeysModal>
        );
    }
}
export default connect(
    state => ({
        areaList: state.zoneSettingData.areaList
    }),
    {
        updateAreaList,
        updateSelectCard,
        updateSelectArea
    }
)(AddNotMetaDataModal);
