import React, { Component } from 'react';
import { DropTarget } from 'react-dnd';

const areaTarget = {
    drop(props, monitor, component){
        const dragItem = monitor.getItem();
        //
        if (dragItem.type === 'card') {
            props.onDrop(monitor);
        }
    }
};

//DropTarget
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver()
}))
class FormGroupArea extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        const { 
            group, 
            isActive,
            contentVisible,
            renderCard,
            onClick,
            renderHeader,
            emptyText,
            connectDropTarget
        } = this.props;
        return connectDropTarget(
            <div
                className={`form-area-group ${isActive ? 'active' : ''}`}
                onClick={onClick}
            >
                {renderHeader}
                <div className="form-area-content" style={{ display: contentVisible ? "block" : "none" }}>
                    {
                        group.queryPropertyList.length > 0 ? group.queryPropertyList.map(renderCard)
                    :
                        <div className="empty-area">
                            <i className="iconfont icon-zhuyi1" />
                            {emptyText}
                        </div>
                    }
                </div>
            </div>
        )
    }
}

export default FormGroupArea;