import { Col, Form, Input, Select, Radio } from "antd";
import React from "react";
import { langCheck } from "Pub/js/utils";
import Notice from "Components/Notice";
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
/**
 * 
 * @param {*} propsData
 * @param {*} DOMDATA
 * @param {*} nodeData
 */
export const createForm = (DOMDATA, propsData) => {
    const children = [];
    DOMDATA &&
        DOMDATA.map((item, index) => {
            let { lable, md = 24, lg = 12, xl = 8 } = item;
            children.push(
                <Col md={md} lg={lg} xl={xl} key={index}>
                    {createFormItem(propsData, item)}
                </Col>
            );
        });

    return children.filter(item => {
        return item.props.children;
    });
};
/**
 * 
 * @param {*} props
 * @param {*} itemInfo
 */
const createFormItem = (props, itemInfo) => {
    let nodeData = props.zoneDatas;
    let { json, zoneFormData } = props;
    const { getFieldDecorator, getFieldsValue } = props.form;
    let { lable, type, code, required, check, search, disabled } = itemInfo;
    const formItemChange = (e) => {
        let formData = {...zoneFormData};
        let val = typeof e === 'string' ? e : e.target.value;
        let newFormData = {...formData, ...{[code]: val}}
        props.setZoneDataFun(newFormData);
        //areaList
        if(code === 'headcode') {
            props.updateAreaList(props.areaList);
        }
    }
    switch (type) {
        case "select":
            if (nodeData.areaList && nodeData.areaList.length > 0) {
                nodeData.areaList.map(item => {
                    if (nodeData[code] === item.code && item.areatype === "0") {
                        Notice({
                            status: "error",
                            msg: langCheck("102202APP-000000", "pages", json)
                        }); /*  */
                        return;
                    }
                });
            }
            return (
                <FormItem label={lable}>
                    {getFieldDecorator(code, {
                        initialValue: nodeData[code] ? nodeData[code] + "" : "",
                        rules: [
                            {
                                required: required,
                                message: langCheck(
                                    "102202APP-000001",
                                    "pages",
                                    json
                                ) /*  */
                            },
                            {
                                validator: check ? check : null
                            }
                        ]
                    })(<Select onChange={formItemChange}>{itemInfo.options}</Select>)}
                </FormItem>
            );
            break;
        case "radio":
            return (
                <FormItem label={lable}>
                    {getFieldDecorator(code, {
                        initialValue: nodeData[code] ? nodeData[code] : false,
                        rules: [
                            {
                                required: required
                            },
                            {
                                type: "boolean",
                                validator: check ? check : null
                            }
                        ]
                    })(
                        <RadioGroup onChange={formItemChange}>
                            <Radio value={true}>
                                {langCheck(
                                    "102202APP-000069",
                                    "pages",
                                    json
                                ) /* */}
                            </Radio>
                            <Radio value={false}>
                                {langCheck(
                                    "102202APP-000070",
                                    "pages",
                                    json
                                ) /* */}
                            </Radio>
                        </RadioGroup>
                    )}
                </FormItem>
            );
            break;
        default:
            return (
                <FormItem label={lable}>
                    {
                        disabled ? <span>{nodeData[code]}</span> : 
                        getFieldDecorator(code, {
                            initialValue: nodeData[code] ? nodeData[code] + "" : "",
                            rules: [
                                {
                                    required: required,
                                    message: `${langCheck(
                                        "102202APP-000002",
                                        "pages",
                                        json
                                    )}${lable}` /*  */
                                },
                                {
                                    validator: check ? check : null
                                }
                            ]
                        })(
                            <Input onChange={formItemChange} />
                        )
                    }
                </FormItem>
            );
            break;
    }
};
