import React, { Component } from 'react';
import { langCheck } from 'Pub/js/utils';
import DragArea from '../DragArea';
import NotMetaItem from './NotMetaItem';
import { dataTypeObj } from "../../utilService";

class NotMetaData extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        const { json } = this.props;
        const notMetaData = dataTypeObj();
        return (
            <DragArea 
                headerTitle={langCheck('ZoneSetting-000202', 'pages', json)}/*   */
            >
                {
                    notMetaData.map(item => {
                        return <NotMetaItem items={item}/>
                    })
                }
            </DragArea>
        );
    }
}

export default NotMetaData;