import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Checkbox, Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import DefdocListGridRef from "Components/Refers/DefdocListGridRef";
import { langCheck } from "Pub/js/utils";
import { getMulti } from "Pub/js/getMulti";
//
class CustomModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            initVal: this.props.initVal,
            refname: this.props.refname,
            iscodeVal: this.props.iscodeVal,
            isgrade: "", // Y: N:
            json: {}
        };
    }
    //
    componentWillReceiveProps(nextProps) {
        // porps,render
        this.setState({
            initVal: nextProps.initVal,
            refname: nextProps.refname,
            iscodeVal: nextProps.iscodeVal
        });
    }
    //
    showModalHidden = () => {
        this.props.setModalVisibel("customModalVisibel", false);
    };
    //dataval
    onOkDialog = () => {
        let { initVal, iscodeVal, refname, isgrade } = this.state;
        this.props.handleSelectChange({
            dataval: initVal,
            iscode: iscodeVal,
            refname,
            defaultvalue: "",
            isgrade
        });
        this.showModalHidden();
    };

    componentDidMount() {
        //
        let callback = json => {
            // console.log('json', json);
            this.setState({
                json: json
            });
        };
        getMulti({
            moduleId: "ZoneSetting",
            // currentLocale: 'zh-CN',
            domainName: "workbench",
            callback
        });
    }

    //iscode
    checkboxChange = e => {
        let val = e.target.checked;
        this.setState({ iscodeVal: val });
    };

    render() {
        let { initVal, iscodeVal, refname, json } = this.state;
        return (
            <div className="myZoneModal">
                <HotKeysModal
                    closable={false}
                    title={langCheck(
                        "ZoneSetting-000022",
                        "pages",
                        json
                    )} /*  */
                    // mask={false}
                    wrapClassName="zonesetting-customModal"
                    visible={this.props.modalVisibel}
                    onOk={this.onOkDialog}
                    destroyOnClose={true}
                    onCancel={this.showModalHidden}
                    // onChange={this.props.refname}
                    footer={[
                        <Tooltip
                            placement="top"
                            overlayClassName="tootip-default"
                            title={`${langCheck(
                                "ZoneSetting-000005",
                                "pages",
                                json
                            )}(Alt + Y)`}
                        >
                            <Button
                                key="submit"
                                type="primary"
                                onClick={this.onOkDialog}
                            >
                                {langCheck("ZoneSetting-000005", "pages", json)}
                                (<span className="underline">Y</span>)
                                {/* /*  */}
                            </Button>
                        </Tooltip>,
                        <Tooltip
                            placement="top"
                            overlayClassName="tootip-default"
                            title={`${langCheck(
                                "ZoneSetting-000006",
                                "pages",
                                json
                            )}(Alt + N)`}
                        >
                            <Button key="back" onClick={this.showModalHidden}>
                                {langCheck("ZoneSetting-000006", "pages", json)}
                                (<span className="underline">N</span>)
                                {/* /*  */}
                            </Button>
                        </Tooltip>
                    ]}
                >
                    <div className="def-refer">
                        <DefdocListGridRef
                            value={{
                                refpk: initVal,
                                refname: refname,
                                refcode: ""
                            }}
                            placeholder={langCheck(
                                "ZoneSetting-000023",
                                "pages",
                                json
                            )} /*  */
                            onChange={val => {
                                this.setState({
                                    initVal: val && val.refpk,
                                    refname: val && val.refname,
                                    isgrade: val && val.values && val.values.isgrade && val.values.isgrade.value
                                });
                            }}
                        />
                    </div>
                    <div className="mdcontent">
                        <span className="refer_label">
                            {langCheck("ZoneSetting-000070", "pages", json)}
                            {/* /*  :*/}
                        </span>
                        <Checkbox
                            checked={iscodeVal}
                            onChange={this.checkboxChange}
                        />
                    </div>
                </HotKeysModal>
            </div>
        );
    }
}
export default connect(
    state => ({
        selectCard: state.zoneSettingData.selectCard
    }),
    {}
)(CustomModal);
