import React, { Component } from 'react';
import { DropTarget } from 'react-dnd';

const areaTarget = {
    drop(props, monitor, component){
        const dragItem = monitor.getItem();
        //
        if (dragItem.type === 'card') {
            props.onDrop(monitor);
        }
    }
};

//DropTarget
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver()
}))
class TableFoldArea extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        const { 
            fold, 
            isActive,
            renderCard,
            visible,
            onClick,
            renderHeader,
            connectDropTarget
        } = this.props;
        return connectDropTarget(
            <div
                className={`form-area-group ${isActive ? 'active' : ''}`}
                style={{ display: visible ? "block" : "none" }}
                onClick={onClick}
            >
                {renderHeader}
                <div className="form-area-content">
                    {fold.queryPropertyList.map(renderCard)}
                </div>
            </div>
        )
    }
}

export default TableFoldArea;