import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Tooltip } from "antd";
import HotKeysModal from "Components/HotKeysModal";
import { updateAreaList, updateBatchSource } from "Store/ZoneSetting/action";
import BatchTable from "./batchTable";
import { langCheck } from "Pub/js/utils";
//
class BatchSettingModal extends Component {
    constructor(props) {
        super(props);
        this.pageSize = 30; //
        this.currentPage = 1; //
        this.totalPage = Math.ceil(this.props.batchSource.length / this.pageSize); //
        this.state = {
            newSource: []
        };
        this.checkAllCheckedObj = {}; //checkbox
    }
    componentDidMount() {
        //30
        let firstBatchSource = this.props.batchSource.slice(0, this.pageSize);
        this.setState({ newSource: firstBatchSource });
    }
    //
    componentWillReceiveProps(nextProps) {
        if (nextProps.batchSettingModalVisibel !== true) {
            return;
        } else {
            // this.setState({ newSource: areaList[areaIndex].queryPropertyList }); //old
            //
            this.setState({ newSource: nextProps.batchSource.slice(0, this.state.newSource.length) });
        }
    }
    //
    showModalHidden = () => {
        this.props.setModalVisibel(false);
        this.currentPage = 1; //
    };
    //
    onOkDialog = () => {
        let { areaList, areaIndex, batchSource } = this.props;
        areaList = _.cloneDeep(areaList);
        batchSource = _.cloneDeep(batchSource);
        // areaList[areaIndex].queryPropertyList = this.state.newSource; //old
        this.setCheckedByCheckAll(batchSource); //checkboxcheckbox
        areaList[areaIndex].queryPropertyList = batchSource;
        this.props.updateAreaList(areaList);
        this.showModalHidden();
    };
    saveNewSource = newSource => {
        let { batchSource } = this.props;
        let newBatchSource = newSource.concat(batchSource.slice(newSource.length, batchSource.length));
        this.setState({ newSource });
        this.props.updateBatchSource(newBatchSource);
    };

    //checkbox
    saveAllNewSource = (newSource, property, value) => {
        let { batchSource } = this.props;
        let newBatchSource = _.cloneDeep(batchSource);
        newBatchSource.map(item => item[property] = value);
        this.setState({ newSource });
        this.props.updateBatchSource(newBatchSource);
    }

    //
    loadNextPage = () => {
        let { newSource } = this.state; 
        let newBatchSource = newSource.concat(this.props.batchSource.slice(newSource.length, newSource.length + this.pageSize));
        this.setCheckedByCheckAll(newBatchSource); //checkboxcheckbox
        this.setState({ newSource: newBatchSource });
    }

    //
    handleScrollTable = () => {
        let $scrollTable = this.batchTableRef && this.batchTableRef.querySelector(".ant-table-body");
        if ($scrollTable && ($scrollTable.scrollTop + $scrollTable.clientHeight >= $scrollTable.scrollHeight * 0.8)) {
            if(this.currentPage < this.totalPage){
                this.currentPage++;
                this.loadNextPage();
            }
        }
    }

    //checkboxcheckbox
    setCheckedByCheckAll = (data) => {
        for (const [key, { checked, indeterminate }] of Object.entries(this.checkAllCheckedObj)) {
            if (!indeterminate) { //
                data.map(item => item[key] = checked);
            }
        }
    }
    render() {
        let { areaIndex, areaList, json, batchSource } = this.props;
        let { newSource } = this.state;
        return (
            <HotKeysModal
                closable={true}
                className="zonesetting-batch-setting-modal"
                style={{ margin: 0 }}
                title={langCheck(
                    "ZoneSetting-000016",
                    "pages",
                    json
                )} /*  -*/
                // mask={false}
                maskClosable={false}
                visible={this.props.batchSettingModalVisibel}
                onOk={this.onOkDialog}
                destroyOnClose={true}
                onCancel={this.showModalHidden}
                width="98%"
                bodyHeight={`${document.body.clientHeight - 110}px`}
                footer={[
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000005",
                            "pages",
                            json
                        )}(Alt + Y)`}
                    >
                        <Button
                            key="submit"
                            type="primary"
                            onClick={this.onOkDialog}
                        >
                            {langCheck("ZoneSetting-000005", "pages", json)}(
                            <span className="underline">Y</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>,
                    <Tooltip
                        placement="top"
                        overlayClassName="tootip-default"
                        title={`${langCheck(
                            "ZoneSetting-000006",
                            "pages",
                            json
                        )}(Alt + N)`}
                    >
                        <Button key="back" onClick={this.showModalHidden}>
                            {langCheck("ZoneSetting-000006", "pages", json)}(
                            <span className="underline">N</span>)
                            {/* /*  */}
                        </Button>
                    </Tooltip>
                ]}
            >
                <div ref={ref => this.batchTableRef = ref} onScrollCapture={this.handleScrollTable}>
                    <BatchTable
                        newSource={newSource}
                        areatype={areaList[areaIndex].areatype}
                        saveNewSource={this.saveNewSource}
                        saveAllNewSource={this.saveAllNewSource}
                        forwardRef={(key, value) => this.checkAllCheckedObj[key] = value}
                    />
                </div>
            </HotKeysModal>
        );
    }
}
export default connect(
    state => ({
        areaList: state.zoneSettingData.areaList,
        batchSource: state.zoneSettingData.batchSource
    }),
    {
        updateAreaList,
        updateBatchSource
    }
)(BatchSettingModal);
