import React, { Component } from 'react';
import { connect } from "react-redux";
import { DragSource, DropTarget } from 'react-dnd';
import { updateSelectArea } from "Store/ZoneSetting/action";
import { removeSelectCardCls } from '../../events';

const areaSource = {
    beginDrag(props) {
        if (!props.isImplement) {
            props.updateSelectArea(props.grandson); //
            removeSelectCardCls(); //
        }
        return {
            id: props.grandson.pk_area,
            index: props.grandsonIndex,
            type: 'area',
            areaType: 'grandson' //
        };
    }
};

const areaTarget = {
    drop(props, monitor, component){
        const dragItem = monitor.getItem();
        //
        if (dragItem.type === 'card') {
            props.onDrop(monitor);
        } else if (dragItem.from !== 'left') {
            const dragIndex = dragItem.index;
            const hoverIndex = props.grandsonIndex;

            // Don't replace items with themselves
            if (dragIndex === hoverIndex || props.isImplement) return;

            props.onDragArea(dragIndex, hoverIndex);
            monitor.getItem().index = hoverIndex;
        }
    }
};
@DragSource('area', areaSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging()
}))
//DropTarget
@DropTarget('area', areaTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver()
}))
class GrandsonArea extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        const {
            areaClassName,
            grandson, 
            isActive,
            renderCard,
            contentVisible,
            renderHeader,
            onClick,
            connectDragSource,
            connectDropTarget
        } = this.props;
        return connectDragSource(
                connectDropTarget(
                    <div className={areaClassName} onClick={onClick}>
                        {renderHeader}
                        {
                            <div className="area-item-content" style={{ display: contentVisible ? "block" : "none" }}>
                                <div className="table-area-content">
                                    {grandson.queryPropertyList.map(renderCard)}
                                </div>
                            </div>
                        }
                    </div>
                )
        )
    }
}

export default connect(
    () => ({}),
    {
        updateSelectArea
    }
)(GrandsonArea);