import React, { Component } from 'react';
import withDragDropContext from "Pub/js/withDragDropContext";
import AreaType from "./components/AreaType";
import NotMetaData from "./components/NotMetaData";
import SetTemplateLeft from "./SetTemplate/setTemplateLeft";

class MyLeftSider extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    
    render(){
        const { show } = this.props;
        return (
            <div className="template-setting-left-sider-container">
                <div 
                    className={`toggle-left-btn ${show ? 'hide' : 'show'}`} 
                    style={{ left: show ? 257 : -11 }}
                    onClick={this.props.onToggleBtnClick} 
                >
                    <i className={`iconfont ${show ? 'icon-jiantouzuo' : 'icon-jiantouyou'}`}></i>
                </div>
                <div className="template-setting-sider template-setting-left-sider" style={{display: show ? 'block': 'none'}}>
                    <SetTemplateLeft isImplement={this.props.status}/>
                    {!this.props.status && <AreaType json={this.props.json}/>}
                    <NotMetaData json={this.props.json}/>
                </div>
            </div>
        )
    }
}
export default withDragDropContext(MyLeftSider);