import React, { Component, PureComponent } from "react";
import platform from "Pub/js/platform";
import { createScript } from "./utilService";
const { base, high } = platform;
const { 
    NCInput, 
    NCFormControl,
    NCRadio, 
    NCCheckbox, 
    NCSelect, 
    NCSwitch,
    NCDatePicker, 
    NCNumber, 
    NCBetweenNum, 
    NCTextArea,
    NCTZDatePickerStart,
    NCTZDatePickerRangeDay,
    NCTZDatePickClientHourTime,
    NCTZDatePickClientTime,
    NCTimepicker,
    NCTZDatePickerEnd,
    NCDatePickerNoTimeZone
} = base;
const { Refer } = high;
const RadioGroup = NCRadio.NCRadioGroup;
const Option = NCSelect.NCOption;

/**
 * 
 *
 * @param {*} item - 
 * @param {*} type - 
 * @param {*} multiLang - 
 * @returns
 */
export function getItemTypeCom(item, type, multiLang) {
    const { disabled, label, onClick } = item;
    const showSelectArr = ['radio', 'checkbox_switch', 'checkbox', 'switch_browse', 'switch']; //select
    let itemDisabled = item.disabled;
    let itemOptions = item.datatype === '32' ? 
        [`${multiLang['ZoneSetting-000229']}=Y`, `${multiLang['ZoneSetting-000230']}=N`] 
        : item.showdataval && item.showdataval.split(","); //options
    const commonProps = {
        disabled,
        placeholder: type === 'search' && label, //placeholder
        onClick
    }
    let result = {
        input: (<NCInput onChange={() => { }} {...commonProps} />),
        residtxt: (<NCInput onChange={() => { }} {...commonProps} />), //
        password: (
            <NCFormControl
                value={this.state.passwordValue}
                onChange={val => this.setState({ passwordValue: val.split("").fill("").join("") })} //
                {...commonProps}
            />),// 
        label: (<NCFormControl isViewMode={true} {...commonProps}/>),
        number: (() => {
            let scale = -1;
            if (item.queryOperateType === "between") {
                return (
                    <NCBetweenNum
                        start={{
                            scale: Number(scale),
                            max: item.max,
                            min: item.min,
                            placeholder: ""
                        }}
                        end={{
                            scale: Number(scale),
                            max: item.max,
                            min: item.min,
                            placeholder: ""
                        }}
                    />
                );
            } else {
                return (
                    <NCNumber
                        max={item.max}
                        min={item.min}
                        suffix={item.suffix}
                        scale={Number(scale)}
                        formNumber={true}
                        {...commonProps}
                    />
                );
            }
        })(),
        textarea: (<NCTextArea style={{ resize: 'none' }} {...commonProps} />),
        radio: (() => {
            let radioItem = <NCRadio
                color="info"
                disabled={itemDisabled}
            />
            if (itemOptions && itemOptions.length > 0){
                radioItem = itemOptions.map((item, i) => {
                    let ops = item.indexOf("=") > 0 ? item.split("=") : [];
                    return (
                        <NCRadio
                            color="info"
                            disabled={itemDisabled}
                            value={ops[1]}
                            key={i}
                        >
                            {ops[0]}
                        </NCRadio>
                    );
                })
            }
            return <RadioGroup
                selectedValue={this.state.radioSelectedValue}
                onChange={val => this.setState({ radioSelectedValue: val })}
            >
                {radioItem}
            </RadioGroup>
        })(),
        checkbox_switch: (
            <NCCheckbox
                className="single-checkbox"
                disabled={itemDisabled}
                colors="primary"
                type="switch"
            />
        ),
        checkbox: (() => {
            if (itemOptions){
                return itemOptions && itemOptions.map((item, i) => {
                    let ops = item.indexOf("=") > 0 ? item.split("=") : [];
                    return <NCCheckbox
                        disabled={itemDisabled}
                        colors="primary"
                        // checked={e.checked}
                        key={i}
                    >
                        {ops[0]}
                    </NCCheckbox>
                })
                
            }else{
                return <NCCheckbox
                    className="single-checkbox"
                    disabled={itemDisabled}
                    colors="primary"
                />
            }
        })(),
        NCTZDatePickerStart: (<div className="item-wrapper"><NCTZDatePickerStart {...commonProps} /></div>),
        NCTZDatePickerRangeDay: (
            <div className="item-wrapper">
                <NCTZDatePickerRangeDay  
                    format={item.format || "YYYY-MM-DD HH:mm:ss"} 
                    {...commonProps}
                />
            </div>
        ),
        NCTZDatePickClientHourTime: (
            <div className="item-wrapper">
                <NCTZDatePickClientHourTime
                    format={item.format || 'YYYY-MM-DD HH:mm:ss'}
                    {...commonProps}
                />
            </div>
        ),
        datetimepicker: (<div className="item-wrapper"><NCTZDatePickClientTime {...commonProps} /></div>),
        timepicker: (<div className="item-wrapper"><NCTimepicker format={"HH:mm:ss"} {...commonProps} /></div>),
        NCTZDatePickerEnd: (<div className="item-wrapper"><NCTZDatePickerEnd {...commonProps} /></div>),
        datePickerNoTimeZone: (<div className="item-wrapper"><NCDatePickerNoTimeZone {...commonProps}/></div>),
        select: (
            <div className="item-wrapper">
                <NCSelect
                    multiple={!!item.isMultiSelectedEnabled}
                    labelInValue={true}
                    {...commonProps}
                >
                    {itemOptions &&
                        itemOptions.map((item, i) => {
                            let ops = item.indexOf("=") > 0 ? item.split("=") : [];
                            return <Option
                                title={ops[0]}
                                value={ops[1]}
                                key={i}
                            >
                                {ops[0]}
                            </Option>
                        })}
                </NCSelect>
            </div>
        ),
        datepicker: (<div className="item-wrapper"><NCDatePicker {...commonProps} /></div>),
        switch_browse: (<NCSwitch disabled={itemDisabled} />),
        switch: (<NCSwitch disabled={itemDisabled}/>),
        refer: (<BaseRefer item={item} {...commonProps}/>)
    };
    let newItemType = type === 'search' && showSelectArr.includes(item.itemtype) ? 'select' : item.itemtype; //
    return result[newItemType];
}
class BaseRefer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            defaultvalueObj: {}
        };
    }

    renderRefer = () => {
        const { item, ...otherProps } = this.props;
        const { refcode } = this.props.item;
        if (this.state[`myRefDom${refcode}`]) {
            //
            const myRefDom = this.state[`myRefDom${refcode}`];
            const tmpRefDom = myRefDom();
            return (
                <Refer
                    foolValue={this.state.defaultvalueObj}
                    onChange={(ref, foolValue) => {
                        this.setState({
                            defaultvalueObj: foolValue
                            // defaultvalueObj: {...ref, ...foolValue}//
                        });
                    }}
                    showHistory={false}
                    {...tmpRefDom.props}
                    {...otherProps}
                />
            );
        } else {
            //
            createScript.call(this, `${refcode}`);
        }
    }
    //
    renderFakeRefer = () => (
        <div
            className={`refer-wrapper ${this.props.disabled ? 'refer-wrapper-disabled' : ''}`}
            onMouseEnter={this.renderRefer}
        >
            <div className="refer" style={{ backgroundColor: window.top.nccColor == 'black' ? 'unset' : this.props.disabled ? '#f3f3f3': '#fff' }}>
                <div className="u-form-control-wrapper">
                    <input type="text" class="nc-input refer-input u-form-control md" placeholder={this.props.placeholder} disabled={this.props.disabled}/>
                </div>
                <i className="icon-refer" style={{ cursor: 'pointer' }}></i>
            </div>
            {/*  */}
        </div>
    )

    render(){
        const { item } = this.props;
        let finalRefer;
        if (item.visible){ //
            finalRefer = item.refcode ? this.renderRefer() : this.renderFakeRefer();
        }else {
            if(this.state[`myRefDom${item.refcode}`]){ //
                finalRefer = this.renderRefer();
            }else{ //
                finalRefer = this.renderFakeRefer();
            }
        }
        //refcode
        if (!finalRefer) finalRefer = this.renderFakeRefer();
        return <div>{finalRefer}</div>
    }
}
